namespace zt.tips {
    import Sprite = Laya.Sprite;
    import Tween = Laya.Tween;
    import Handler = Laya.Handler;
    import Loader = Laya.Loader;
    import Texture = Laya.Texture ;
    import RaiseParams = zt.RaiseParams;
    export class RaisingImageManager 
    {
        private _layer: Sprite;

        /**
         *  
         */
        private _waitsQuene: string[];
        private _waitParamsQuene: any[];
        private _busy: boolean;
        private static _instance: RaisingImageManager;
        
        /**
         * @param url 
         * @param param 
         */
        add(url:string,raiseParam:RaiseParams) 
        {        
            if(raiseParam.isQuene)
            {
                this._waitsQuene = this._waitsQuene || [];
                this._waitParamsQuene = this._waitParamsQuene || [];
                this._waitsQuene.push(url);
                this._waitParamsQuene.push(raiseParam);
                this.checkNext();
            }else
            {
                this.playRaise(url,raiseParam);
            }
        }

        private checkNext(): void 
        {
            this._waitsQuene.length > 0 && this.next();
        }

        private next(): void 
        {
            this._busy = true;
            const url: string = this._waitsQuene.shift();
            const param: RaiseParams = this._waitParamsQuene.shift();
            Laya.timer.once(param.detalTime,this,this.checkNext);
            this.playRaise(url,param);
        }

        public playRaise(url:string,param:RaiseParams):void
        {
            const raiseItem:RaiseItem = this.getRaiseItem(url,param.isQuene);
            raiseItem.x = param.start.x ;
            raiseItem.y = param.start.y ;
            this._layer.addChild(raiseItem);
            const toX:number = param.end.x;
            const toY:number = param.end.y;
            const duration = param.duration;
            raiseItem.alpha = 1 ;
            Tween.to(raiseItem, {x:toX,y:toY,alpha:0.6}, duration, null, Handler.create(this, this.oncomplete, [raiseItem]));
        }

        /**
         * item
         * @param url 
         */
        private getRaiseItem( url: string,isQuene:boolean = true): RaiseItem 
        {
            let raiseItem : RaiseItem = Laya.Pool.getItemByClass('raiseItem',RaiseItem);
            raiseItem.url = url ;
            raiseItem.isQuene = isQuene ;
            return raiseItem;
        }

        private oncomplete(raiseItem: RaiseItem): void 
        {
            raiseItem.clear();
            Laya.Pool.recover('raiseItem',raiseItem);
            console.log(`${raiseItem.url} on completed.`)
        }

        static get instance(): RaisingImageManager 
        {
            return RaisingImageManager._instance || (RaisingImageManager._instance = new RaisingImageManager());
        }

        public init(layer: Sprite): void {
            this._layer = layer;
        }

    }

    /**
     * 
    */
    export class RaiseItem extends Sprite
    {
        private _url:string ;
        private _isQuene:boolean = false;
        constructor($url:string)
        {
            super();
        }

        public set url(value:string)
        {
            this._url = value ;
            const t: Texture =  Laya.loader.getRes(value);
            if(t)
            {
                this.graphics.drawTexture(t);
            }else
            {
                Laya.loader.load(this._url,Handler.create(this,this.loadComplete));
            }
        }

        private loadComplete():void
        {
            const t: Texture =  Laya.loader.getRes(this.url);
            if(t)
            {
                this.graphics.drawTexture(t);
            }
        }

        public get url():string
        {
            return this._url;
        }

        public set isQuene(value:boolean)
        {
            this._isQuene = value ;
        }

        public get isQuene():boolean
        {
            return this._isQuene;
        }

        public clear():void
        {
            this.graphics.clear();
            this.removeSelf();
        }

    }
}