/// <reference path='./BaseDataHash.ts' />
namespace zt.manager {
	import DataHash = zt.data.DataHash;
	import BaseDataHash = zt.data.BaseDataHash;
	export class DataManager {
		private static _instance: DataManager;
		private _cache: object;		//
		private _waitPool: object;	//

		constructor() {
			this._cache = {};
			this._waitPool = {};
		}

		static get inst(): DataManager {
			return this._instance || (this._instance = new DataManager());
		}

		/**
		 *  
		 * @param name 
		 * @param data 
		 * @param mainKeys   ["ItemID","Level"]
		 * @param itemCls 
		 * @param hashCls 
		 * @param buildNow 
		 */
		public registSysDataHash(name: string, data: any, mainKeys: any = null, 
			itemCls: any = null, hashCls: any = null, buildNow: Boolean = false): BaseDataHash {
			hashCls = hashCls ? hashCls : BaseDataHash;
			if (!this._cache[name] && buildNow) {
				this._cache[name] = new hashCls(name, data, mainKeys, itemCls);
			} else {
				this._waitPool[name] = [name, data, itemCls, hashCls, mainKeys];
			}
			return this._cache[name];
		}

		/**
		 *  
		 * @param name 
		 */
		public getSysDataHash(name: string): BaseDataHash {
			if (this._waitPool[name]) {
				const arr: Array<any> = this._waitPool[name];
				// tslint:disable-next-line:no-magic-numbers
				const hashCls: any = (arr[3] ? arr[3] : BaseDataHash);
				const data: string = arr[1];
				const itemCls: any = arr[2];
				// tslint:disable-next-line:no-magic-numbers
				const mainKey: any = arr[4];
				const dataHash: BaseDataHash = new hashCls(name, data, mainKey, itemCls);
				delete this._waitPool[name];
				this._cache[name] = dataHash;
			}
			return this._cache[name];
		}

		public getDataHash(name: string): DataHash {
			if (this._waitPool[name]) {
				const arr: Array<any> = this._waitPool[name];
				// tslint:disable-next-line:no-magic-numbers
				const hashCls: any = (arr[3] ? arr[3] : BaseDataHash);
				const data: String = arr[1];
				const itemCls: any = arr[2];
				// tslint:disable-next-line:no-magic-numbers
				const mainKey: any = arr[4];
				const dataHash: BaseDataHash = new hashCls(name, data, mainKey, itemCls);
				delete this._waitPool[name];
				this._cache[name] = dataHash;
			}
			return this._cache[name];
		}

		/**
		 *  
		 * @param name 
		 * @param data 
		 * @param mainKey 
		 * @param itemCls 
		 * @param hashCls 
		 * @param buildNow 
		 */
		public registDataHash(name: string, data: any, mainKey: any, itemCls: any = null, hashCls: any = null, buildNow = false): DataHash {
			hashCls = hashCls ? hashCls : DataHash;
			if (!this._cache[name] && buildNow) {
				this._cache[name] = new hashCls(name, data, mainKey, itemCls);
			} else {
				this._waitPool[name] = [name, data, itemCls, hashCls, mainKey];
			}
			return this._cache[name];
		}
	}
}