namespace zt.effects {
    import Sprite = Laya.Sprite ;
    import BlendMode = Laya.BlendMode;
    import Handler = Laya.Handler ;
    import Loader = Laya.Loader ;
    import Texture = Laya.Texture ;
    import Rectangle = Laya.Rectangle;
    import GraphicAnimation = Laya.GraphicAnimation;
    /**
     * 
     * 
     * @since 2018.03.05
     */
    export class SkillEffect extends Sprite 
    {
        public url:string = '';
        private _module:string;
        private _cacheFrame:object;
        private _currentFrame:number ;
        private _loadCompleteList:Handler[];
        private interval:number;
        private frames:any[];
        private _totalFrame:number;
        private _speed:number ;
        private _isContray:boolean;
        private _length:number;
        private _isLoadComplete:boolean;
        private _rectangle:Rectangle ;
        private _isPlaying:boolean = false;
        private _isStart:boolean = false;
        private _sourceWidth:number;
        private _sourceHeight:number ;
        private _isAttack:Boolean;
        private _startFrame:number ;
        private _isLoop:boolean = false;
        constructor($url: string,$x:number,$y:number,$isContray:boolean,$length:number,$parent: Sprite,$isAttack:boolean) 
        {
            super();
            this.url = $url ;
            this.x = $x ;
            this.y = $y ;
            this._isAttack = $isAttack ;
            this._isContray = $isContray ; 
            this._length = $length ;
            $parent.addChildAt(this,0);
            this.rotation = this._isContray ? 154:-26;
            // this.rotation = this._isContray ? 140:-40;
            Laya.loader.load($url, Handler.create(this, this.loadComplete), null, Laya.Loader.ATLAS);
        }

        /**
         * 
         */
        private loadComplete():void
        {
           this.parseAni();
            if(this._loadCompleteList)
            {
                for(let call of this._loadCompleteList)
                {
                    call.run();
                }
            }
            if(this._isStart && this._isPlaying == false)
            {
                this.play(this._startFrame);
            }
        }

        /**
         * 
         */
        private parseAni():void
        {
            var atlas = Loader.getAtlas(this.url);
            if (!atlas)  return ;
            //
            this._currentFrame = 0 ;
            this.frames = atlas ;
            this._totalFrame = atlas.length ;
            this._isLoadComplete = true ;
        }

        /**
         * 
         */
        private checkPlayComplete():boolean
        {
            if(this._currentFrame >= this._totalFrame )
            {
                if(this._isLoop)
                {
                    this._currentFrame = 0 ;
                    return false;
                }
                this.event('play_complete',this);
                this.dispose();
                return true;
            }
            return false;
        }


        /**
         *  
         * @private
         * @memberof SkillEffect
         */
        private dispose():void
        {
            this._loadCompleteList = [];
            this.graphics.clear();
            this.removeSelf();
        }

        public playEffect(speed:number = 30,isLoop:boolean = false,$blendMode:string = BlendMode.NORMAL,frame:number = 0):void
        {
            this.blendMode = $blendMode||Laya.BlendMode.NORMAL ;
            this.interval = speed ;
            this._isStart = true ;
            this._startFrame = frame ;
            this._isLoop = isLoop ;
            this.play(frame);
        }

        /**
         * 
         * @param frame 
         * @param 
         */
        private play(frame:number = -1):void
        {
            if(!this._isLoadComplete)return;
            this._currentFrame = frame == -1 ? frame:this._startFrame;
            this._isPlaying = true ;
            Laya.timer.loop(this.interval,this,this.loop);
        }

        public get isPlaying():boolean
        {
            return this._isPlaying;
        }


        /**
         * 
         */
        protected loop(): void
        {   
            const playComplete = this.checkPlayComplete();
            const frame:number = this._currentFrame ;
            if(playComplete)
            {
                Laya.timer.clear(this,this.loop);
                return ;
            } ;
            this._isPlaying = true ;
            const isAttack = this._isAttack ;
            const length:number = this._length ;
            const aniUrl:string = this.frames[frame];
            const t:Texture =  Laya.loader.getRes(aniUrl);
            const tw = t.sourceWidth ;
            let x:number = isAttack ? 0 :  tw - length;
            let w:number = length;
            const source:Texture =  Texture.createFromTexture2(t,x + t.offsetX,t.offsetY,w,t.height,t.sourceWidth,t.sourceHeight);
            source.offsetX = t.offsetX;
            source.offsetY = t.offsetY;
            this.graphics.cleanByTexture(source,0,0);
            if(this._cacheFrame)
            {
                if(this._cacheFrame[frame])
                {
                    (this._cacheFrame[frame] as Handler).run();
                    delete this._cacheFrame[frame];
                }
            }
            this._currentFrame ++ ;
        }

        public addFrame($frame:number,$handle:Handler):void
        {
            if(this._cacheFrame == null)
            {
                this._cacheFrame = {};
            }
            this._cacheFrame[$frame] = $handle ;
        }

        /**
         *
         */
        public registerLoadComplete($func:Handler):void
        {
            if(!this._loadCompleteList)
            {
                this._loadCompleteList = [];
            }
            if(this._loadCompleteList.indexOf($func) == -1)
            {
                this._loadCompleteList.push($func);
            }
        }

        /**
         * 
         */
        public stopEffect():SkillEffect
        {
            Laya.timer.clear(this,this.loop);
            this._isPlaying = false;
            return this ;
        }

        public changeSpeed($inteval:number):void
        {
            this.interval = $inteval ;
            Laya.timer.clear(this,this.loop);
            Laya.timer.loop(this.interval,this,this.loop);
        }

        public destroy(destroyChild?: boolean): void
        {
            if(this._cacheFrame)
                this._cacheFrame = null ;
            this.graphics.clear();
            super.destroy(true);
        }

        public get module():string
        {
            return this._module ;
        }

        public set module(value:string)
        {
            this._module = value ;
        }

        public get name():string
        {
            return this.url;
        }
    }
}