namespace zt.scene {
	import Handler = Laya.Handler;
	export class SceneItem extends Laya.EventDispatcher 
	{
		private _name: string;
		private _scene: SceneBase;
		private _params: SceneParams;
		private _resLoaded = false;
		private _dataLoaded = false;
		private _isReBuild:boolean = false;
		public constructor(name: string, scene: SceneBase, params: SceneParams) 
		{
			super();
			this._scene = scene;
			this._name = name;
			this._params = params;
			if(this._params)
			{
				this._params.sceneName = name ;
			}else
			{
				this._params = {sceneName:name};
			}
			ctrl.GameCtrl.intance.event(Scene.CHANGESCENE);//
		}

		public preload() 
		{
			showAppLoading();
			this.loadRes();
			this.loadData();
		}

		public update(params:SceneParams):void
		{
			this._isReBuild = true ;
			this._params = params ;
			this.scene.update(params);
			this.loadData();
		}

		private loadRes(): void 
		{
			if (this._scene.res.length === 0) {
				this.onResLoad();
				return;
			}
			Laya.loader.load(this._scene.res, Handler.create(this, this.onResLoad), Handler.create(this, this.onResProgress, null, false));
		}

		private onResProgress(val: number) 
		{
			updateAppLoading(val);
		}

		private loadData() 
		{
			const calls = new Calls();
			calls.addCmds(this._scene.cmds);
			calls.once(Laya.Event.COMPLETE, this, () => {
				this._dataLoaded = true;
				this.checkComplete()
			});
			calls.start();
		}

		private onResLoad(): void 
		{
			this.event(app.AppEvent.RES_LOAD);
			this._resLoaded = true;
			this.checkComplete();
		}

		private checkComplete() 
		{
			if (this._resLoaded && this._dataLoaded) 
			{
				hideAppLoading();
				this.event(scene.SceneEvent.PRELOAD_COMPLETE);
			}
		}

		get scene(): SceneBase 
		{
			return this._scene;
		}

		get name(): string 
		{
			return this._name;
		}

		get params(): SceneParams 
		{
			return this._params
		}

		dispose() 
		{
			this._scene.dispose();
		}

	}
}