namespace zt.data {

	/***/
	export class BaseDataItem extends Laya.EventDispatcher {

		constructor(d: object) {
			super();
			for (const key in d) {
				this.setPropRead(key, d);
			}
		}

		setPropRead(key: string, d: Object): void {
			/**
			 * value:d[key]
			 * writable:true		false,
			 * enumerable:false		for...inObject.keys
			 * configurable:true	falsevaluewritableconfigurable
			 */
			Object.defineProperty(this, '_' + key, { value: d[key], writable: true, enumerable: false, configurable: true });
			/**
			 * get:function() {return this['_'+key];}
			 * enumerable:false
			 * configurable:true
			 */
			const key1 = key[0].toLowerCase() + key.substring(1, key.length);
			Object.defineProperty(this, key1, { get: function () { return this['_' + key]; }, enumerable: false, configurable: true })
		}
	}
}