namespace zt {

    export class MyArray
    {
        public static DESCENDING = 2;//()
        public static NUMERIC = 16;//

        /**
         * 
         * @param arr 
         * @param fieldName 
         * @param options DESCENDINGNUMERIC
         */
        public static sortOn(arr: any[],fieldName:any,options:any = null):void
        {
            if(fieldName instanceof Array)
            {
                if(options == null)
                {
                    options = [];
                }
                this.listSortFunc(arr,fieldName,options);
            }
            else
            {
                if(options == null)
                {
                    options = 0;
                }
                this.oneSortFunc(arr,fieldName,options);
            }
        }

        /** */
        private static oneSortFunc(arr: any[],fieldName:string,options:number):void
        {
            arr.sort((a:any,b:any) => {
                return this.sortFunc(a[fieldName],b[fieldName],options);
            });
        }

        /** */
        private static listSortFunc(arr: any[],fieldNameList:string[],optionsList:number[]):void
        {
            arr.sort((a:any,b:any) => {
                for(let i = 0;i < fieldNameList.length;i++)
                {
                    const fieldName = fieldNameList[i];
                    const options = optionsList[i] != null ? optionsList[i] : 0;
                    const sortValue = this.sortFunc(a[fieldName],b[fieldName],options);
                    if(sortValue != 0)
                    {
                        return sortValue;
                    }
                }
                return 0;
            });
        }

        /** */
        private static sortFunc(a:any,b:any,options:number):number
        {
            if(this.isNumeric(options))
            {
                const tempA:number = a instanceof String ? parseFloat(<string>a) : a;
                const tempB:number = b instanceof String ? parseFloat(<string>b) : b;
                return this.numberCompare(tempA,tempB,options);
            }
            return this.stringCompare(a,b,options);
        }

        /** */
        private static numberCompare(a:number,b:number,options:number):number
        {
            if(this.isDescending(options))
            {
                return b - a;
            }
            return a - b;
        }

        /** */
        private static stringCompare(a:string,b:string,options:number):number
        {
            if(a > b)
            {
                return this.isDescending(options) ? -1 : 1;
            }
            else if(a < b)
            {
                return this.isDescending(options) ? 1 : -1;
            }
            else
            {
                return 0;
            }
        }

        /** */
        private static isDescending(options:number):boolean
        {
            return (options & MyArray.DESCENDING) != 0;
        }

        /** */
        private static isNumeric(options:number):boolean
        {
            return (options & MyArray.NUMERIC) != 0;
        }
    }

}