namespace zt.tips {
	export class TipCaches {
		private static _instance: TipCaches;
		private static _cls: object = {};
		private static DELAY = 5000;
		private _hash: object;

		/**
		 * tiptip
		 */
		constructor() {
			this._hash = {};
		}

		static getInstance(): TipCaches {
			return this._instance || (this._instance = new TipCaches);
		}

		static register(type: string, cls: any): void {
			TipCaches._cls[type] = cls;
		}

		getTip(type: string): BaseTip {
			Laya.timer.clear(this, this.clear);
			if (this._hash[type] == null) {
				this._hash[type] = this.getTipByType(type);
			}
			return this._hash[type];
		}

		private getTipByType(type: string): BaseTip {
			if (TipCaches._cls[type] == null) throw new Error(`tip class[${type}] not found.`);
			return new TipCaches._cls[type];
		}

		clearCache(): void {
			this.clear();
			// Laya.timer.once(TipCaches.DELAY, this, this.clear);
		}

		private clear(): void {
			this._hash = {};
		}
	}
}