namespace zt.effects {
    import Sprite = Laya.Sprite ;
    import BlendMode = Laya.BlendMode;
    import Handler = Laya.Handler ;

    /**
     * 
     * 
     */
    export class Effect extends Laya.Animation 
    {
        public isOnce:boolean = false;
        public url:string = '';
        private _callBack:Function;
        public endToRemove:boolean;
        private _module:string;
        private _isSingle:boolean = false;
        private _cacheFrame:object;
        private _currentFrame:number ;
        private _loadCompleteList:Handler[];
        private _isCenter:boolean = false;
        private _scale:number;
        private _container:any;
        constructor($url: string,$once:boolean,$endToRemove:boolean,$callBack:Function = null) 
        {
            super();
            this._count = 2147483648;
            this.url = $url ;
            this.loadAtlas($url,Handler.create(this,this.loadComplete));
            this.isOnce = $once;
            this.endToRemove = $endToRemove;
            this._callBack = $callBack;
            this.on(Laya.Event.COMPLETE,this,this.onPlayComplete);
        }


        private loadComplete():void
        {
            if(this._loadCompleteList)
            {
                for(let call of this._loadCompleteList)
                {
                    call.run();
                }
            }
            if(this.isCenter)
            {
                const bounds: Laya.Rectangle = this.getGraphicBounds();
                this.x = this._container['width'] / 2 -  bounds.width * this._scale / 2;
                this.y = this._container['height']  / 2 -bounds.height * this._scale / 2;
            }
        }

        private onPlayComplete():void
        {
            if(this.frames && this.frames.length > 0)
                this.event('play_complete',this);
        }


        /**
         * @param  parent 
         * @param  x x pos
         * @param  y y pos
         * @param isLoop  endToMove
         * @param  blendMode blendMode 
         */
        playEffect($parent: Sprite,$x:number,$y:number,$isLoop:boolean = false,$blendMode:string = BlendMode.NORMAL,$callBack:Function = null,speed:number = 30):void
        {
            if($callBack)
                this.callBack = $callBack ;
            this.x = $x ;
            this.y = $y ;
            this.blendMode = $blendMode||Laya.BlendMode.NORMAL ;
            $parent.addChild(this);
            this.interval = speed ;
            this.play(1,$isLoop);
        }

        /**
         * 
         * @param 
         * @param  
         * @param  
         * @param  
         * @param speed 
         */
        playEffectAtCenter($parent: Sprite,$isLoop:boolean = false,$blendMode:string = BlendMode.NORMAL,speed:number = 60,scale:number = 1):void
        {
            this._isCenter = true;
            this.blendMode = $blendMode ;
            this._container = $parent ;
            this._scale = scale;
            $parent.addChild(this);
            this.play(1,$isLoop);
            this.interval = speed ;
            if(this.isCenter)
            {
                const bounds: Laya.Rectangle = this.getGraphicBounds();
                this.x = this._container['width'] / 2 -bounds.width * scale/ 2;
                this.y = this._container['height']  / 2 -bounds.height * scale/ 2;
            }
        }


        /**
         * 
         */
        protected _frameLoop(): void
        {   
            if(this._cacheFrame)
            {
                if(this._cacheFrame[this._index])
                {
                    (this._cacheFrame[this._index] as Handler).run();
                    delete this._cacheFrame[this._index];
                }
            }
            super._frameLoop();
        }

        addFrame($frame:number,$handle:Handler):void
        {
            if(this._cacheFrame == null)
            {
                this._cacheFrame = {};
            }
            this._cacheFrame[$frame] = $handle ;
        }

        /**
         *
         */
        registerLoadComplete($func:Handler):void
        {
	     if(!this._loadCompleteList)
            {
                this._loadCompleteList = [];
            }
            if(this._loadCompleteList.indexOf($func) == -1)
            {
                this._loadCompleteList.push($func);
            }
        }

        /**
         * 
         */
        stopEffect():Effect
        {
            this.stop();
            return this ;
        }

        changeSpeed($inteval:number):void
        {
            this.interval = $inteval ;
        }

        destroy(destroyChild?: boolean): void
        {
            this.off(Laya.Event.COMPLETE,this,this.onPlayComplete);
            if(this._cacheFrame)
                this._cacheFrame = null ;
            super.destroy(true);
        }

        set module(value:string)
        {
            this._module = value ;
        }

        get module():string
        {
            return this._module ;
        }

        set isSingle(value:boolean)
        {
            this._isSingle = value ;
        }

        get isSingle():boolean
        {
            return this._isSingle  ;
        }

        set callBack(value:Function)
        {
            this._callBack = value ;
        }

        get callBack():Function
        {
            return this._callBack ;
        }

        get isCenter():boolean{
            return this._isCenter;
        }

        set isCenter(value:boolean){
            this._isCenter = value ;
        }

        get name():string
        {
            return this.url;
        }
    }
}