			// 	// console.log('startx : starty' ,this.x ,this.y );
			// 	// console.log("speedX",speedX,"speedY",speedY);
			// 	// console.log('movex : movey' ,movex ,movey );
			// 	// console.log('endx : endy' ,endx ,endy );
			// 	if(endx < -this.universeData.universeWidth + stageW)
			// 	{
			// 		endx = -this.universeData.universeWidth + stageW;
			// 	}
			// 	if(endy < -this.universeData.universeHeight + stageH)
			// 	{
			// 		endy = -this.universeData.universeHeight + stageH;
			// 	}
