/**
 * 
 */
namespace zt.utils {
	export class stringUtil {
		/**
		 * {number}
		 * a{0}c...b -> abc
		 */
		static paramFormat(str: any, ...paramList): any {
			if(!!str == false)	return '';
			if (typeof str === 'string')
				return stringUtil.paramFormatItem(str, paramList);
			return (<string[]>str).map(i =>
				stringUtil.paramFormatItem(i, paramList)
			);
		}

		private static paramFormatItem(str: string, paramList): string {
			let g: RegExp;
			for (let i = 0; i < paramList.length; i++) {
				g = new RegExp('\\{' + i + '\\}', 'g');
				str = str.replace(g, paramList[i])
			}
			return str;
		}

		/**
		 * 
		 */
		static isNumeric(p_string: any): boolean {
			if (p_string == null) {
				return false;
			}
			const regx: RegExp = /^[-+]?\d*\.?\d+(?:[eE][-+]?\d+)?$/;
			return regx.test(p_string);
		}

		/**
		 * 
		 */
		static trim(p_string: string): string {
			if (p_string == null) {
				return '';
			}
			return p_string.replace(/^\s+|\s+$/g, '');
		}

		/**
		 *  
		 */
		static replaceFirstUper(str: string): string {
			const reg = /\b(\w)|\s(\w)/g; //  \b\s
			return str.replace(reg, (m) => m.toUpperCase());
		}

		/**
		 *  
		 */
		static replaceFirstLower(str: string): string {
			const reg = /\b(\w)|\s(\w)/g; //  \b\s
			return str.replace(reg, (m) => m.toLowerCase());
		}
	}
}
