/**
 * 
 */
namespace zt.utils {
	export class ArrayUtil {

		/**  */
		static NUMERIC  = 16;
		/**  */
		static DESCENDING = 2;

		/**
		 * listitem
		 */
		static contains(item:any,list:Array<any>):Boolean {
			return list.indexOf(item) != -1;
		}
		
		/**
		 * 
		 * @param item 
		 * @param list 
		 */
		static remove(item:any,list:Array<any>):any {
			const index:number = list.indexOf(item);
			if(index != -1)
				return list.splice(index,1)[0];
			return null;
		}

		/**
		 * key
		 * @param object 
		 */
		static keys(object) {
			var keys = [];
			for (var property in object)
				keys.push(property);
			return keys;
		}

		 /**  3*/
		 static sortOnArr(arr,keyArr:string[], order:number[])
		 {
			 if(keyArr == undefined || order == undefined )
			 {
				 return ;
			 }
			 if(keyArr.length != order.length || keyArr.length == 0)
			 {
				 return ;
			 }
			 arr.sort((a:any,b:any):number=>{
				 if(a[keyArr[0]] >b[keyArr[0]])
					 return order[0]==ArrayUtil.NUMERIC?1:-1;
				 if(a[keyArr[0]] <b[keyArr[0]])
					 return order[0]==ArrayUtil.NUMERIC?-1:1;
				 if(keyArr.length>1)
				 {
					 if(a[keyArr[1]] >b[keyArr[1]])
						 return order[1]==ArrayUtil.NUMERIC?1:-1;
					 if(a[keyArr[1]] <b[keyArr[1]])
						 return order[1]==ArrayUtil.NUMERIC?-1:1;
					 if(keyArr.length>2)
					 {
						 if(a[keyArr[2]] >b[keyArr[2]])
							 return order[2]==ArrayUtil.NUMERIC?1:-1;
						 if(a[keyArr[2]] <b[keyArr[2]])
							return order[2]==ArrayUtil.NUMERIC?-1:1;
					 }
				 }                
				 return 0;
			 })
		}
	}
}