namespace zt.app
{

    /**
     * 
     * @author wangcongyan 
     * @since 2017.12.30
     */
    export class UIModel implements IModel
    {   
        private _modelName:string = 'UIView';
        private _prefix:string;
        protected _params:any;
        private _moduleName:string ;
        constructor($param:any)
        {
            this._params = $param;
        }

        public init():void
        {
            
        } 

        public dispose():void
        {
            throw new Error('dispose function must be override!');
        } 

        public getData():object
        {
            throw new Error('getData function must be override!');
        }

        public start():void
        {
            
        }
        
        public setModuleName(moduleName:string,$prefix:string):void
        {
            this._prefix = $prefix ? $prefix : '';
            this._moduleName = moduleName ;
            this._modelName = getNameByModuleAndprefix(moduleName,'model',this._prefix);
        }

        public get moduleName():string
        {
            return this._moduleName; 
        }

        public get modelName():string
        {
            return this._modelName ;
        }

        public get prefix():string
        {
            return this._prefix;
        }
    }
}