namespace zt.net {
	import Handler = Laya.Handler;
	import Byte = Laya.Byte;
	/**
	 * call 
	 */
	export const call = (cmd: string, params: Object, callback: Handler = null, errorHandler: Handler = null, noMask = false) => {
		!noMask && showMask();
		const msgHead: MessageHeadOut = new MessageHeadOut(
			MessageType.ENTITY,
			ComponentType.CLIENT,
			WebParams.userId,
			ComponentType.GATEWAYAPP, GetwayAppMgr.list[0].id);
		GameSocket.instance.send(msgHead, { Cmd: cmd, Content: params },
			Handler.create(null, (data) => {
				!noMask && hideMask();
				callback && callback.runWith(data);
			}),
			Handler.create(null, (data) => {
				!noMask && hideMask();
				errorHandler && errorHandler.runWith(data);
			}))
	}

	export const on = (cmd: string, caller, method) => {
		GameSocket.instance.on(cmd, caller, method);
	}
	export const off = (cmd: string, caller, method) => {
		GameSocket.instance.off(cmd, caller, method);
	}

	export const onSeq = (cmd: string, cb: Handler, onerror?: Handler): Function => {
		const onevent = (data) => {
			if (data.Code > 0 && onerror)
				onerror.runWith(data.Code);
			if (data.Code <= 0 && cb)
				cb.runWith(data.Content)
		};
		const eventname: string = EventUtils.getSeqEventName(cmd);
		GameSocket.instance.on(eventname, null, onevent);
		return onevent;
	}
	export const onSeqOff = (cmd: string, onevent) => {
		const eventname: string = EventUtils.getSeqEventName(cmd);
		GameSocket.instance.off(eventname, null, onevent);
	}

	export const callCompMsg = (toCompType: number, toCompID: number, toCompCMD: number, data: any,
		callback: Handler, errorHandler: Handler) => {
		const msgHead: MessageHeadOut = new MessageHeadOut(MessageType.COMPONENT,
			ComponentType.CLIENT, WebParams.userId, toCompType, toCompID);
		GameSocket.instance.sendComponentMsg(msgHead, toCompCMD, data, callback, errorHandler);
	}

	export const getByteObj = (data: any = null): Byte => {
		const result: Byte = new Byte(data);
		result.endian = Byte.BIG_ENDIAN;
		return result;
	}

	export class EventUtils {
		public static getSeqEventName(cmd: string): string {
			return `_SEQ_${cmd}`;
		}
		public static getSeqEventNames(cmds: string[]): string[] {
			const result: string[] = [];
			for (const i of cmds) {
				result.push(EventUtils.getSeqEventName(i));
			}
			return result;
		}
	}
}