namespace zt.effects
{   
    import Rectangle = Laya.Rectangle;
    import Texture = Laya.Texture ;
    /**
     * @author wangcongyan
     * @since 2017.03.01
     * @description 
     * @class DamageFactory
     */
    export class DamageFactory
    {
        /**
		 *   
		 */		
        private static _instance:DamageFactory ;
        public DAMAGE:string = 'd' ;
        public HEAL:string = 'h' ;
        public CRITICAL:string = 'c';
        private _damageType:object = {0:'d',1:'d',2:'c',3:'h'};
        constructor()
        {

        }

        /**
		 *  
		 * @return 
		 */		
		public static get instance():DamageFactory
		{
			if(!this._instance)
			{
				this._instance = new DamageFactory();
			}
			return this._instance ;
        }
        
        /**
		 *  
		 * @param $hurtNum
		 * @param $type
		 * @param $isAdd
		 * @return DamageNum
		 */
		public getDamageView($num:DamageNum,$hurtNum:String,$type:number,$isAdd:Boolean = false,$gap:number = 6 ):void
		{
            var len:number = $hurtNum.length,i:number = 0  ;
            const type:string = this._damageType[$type];
            let url:string;
            let t:Texture;
            let pos:number = 0;
            let urlTemp :string 
            for(let i  = 0 ; i < len; i ++)
            {
                const num = $hurtNum.charAt(i);
                urlTemp = `fight/damage/damageNum/${type}${num}.png`;
                url =  zt.utils.getResourceURL(urlTemp);
                t =  Laya.loader.getRes(url);
                if(!t)
                {
                    console.log('test');
                    continue ;
                }
                $num.graphics.drawTexture(t,pos);
                pos += (t.width + $gap);
            }
		}

        public init()
		{
			
		}
    }


}