namespace zt.tips 
{
    import Sprite = Laya.Sprite;
    import Tween = Laya.Tween;
    import Handler = Laya.Handler;
    import Loader = Laya.Loader;
    import Texture = Laya.Texture ;
    import RaiseParams = zt.RaiseParams;
    import GObject = fairygui.GObject ;
    import Point = Laya.Point ;
    import Effect = zt.effects.Effect;
    export class RaisingItemManager 
    {
        private _layer: Sprite;
        /**
         *  
         */
        private _waitsQuene: IRaseData[];
        private _busy: boolean;
        private static _instance: RaisingItemManager;
        private _registerDic:object = {};
        private _displayItems:any[];
        private _flyItems:any[];
        private _isFlying:boolean = false;
        /**
         * @param item 
         */
        public add(item:IRaseData) 
        {        
            this._waitsQuene.push(item);
        }

        constructor()
        {
            this._displayItems = [];
            this._waitsQuene = [];
            this._flyItems = [];
        }

        public play():void
        {
            if(this._busy)
            {
                return ;
            }
            this.playRaiseItem();
        }

        private arrange():void
        {
            const len = this._displayItems.length ;
            const raiseWidth = 88 * (len + 1) +  50 * len;
            const width:number = Laya.stage.width ;
            const height:number = Laya.stage.height ;
            const startX:number = width/2 -raiseWidth/2 ;
            const startY:number = height/2 - 200;
            let raiseItem:any;
            for(let i = 0 ; i < len ; i ++)
            {
                raiseItem = this._displayItems[i];
                raiseItem.x = startX + i * 138;
                raiseItem.y = startY;
            }
            this.playSingle();
        }

        private playSingle():void
        {
            if(this._displayItems.length > 0)
            {
                const raiseItem = this._displayItems.shift();
                raiseItem.playEffect(Handler.create(this,()=>
                {
                    const toPoint = raiseItem.dragItem.raisePoint;
                    this._flyItems.push(raiseItem);
                    this.playSingle();
                    // Tween.to(raiseItem, {x:toPoint.x,y:toPoint.y}, 600, null, Handler.create(this, this.oncomplete, [raiseItem]));
                }))
            }else
            {
                if(this._isFlying == false)
                {
                    this.flyItem();
                } 
            }
        }

        private flyItem():void
        {
            if(this._flyItems.length > 0)
            {
                this._isFlying = true ;
                const raiseItem = this._flyItems.shift();
                const toPoint = raiseItem.dragItem.raisePoint;
                Tween.to(raiseItem, {x:toPoint.x,y:toPoint.y}, 300, null, Handler.create(this, this.oncomplete, [raiseItem]));
            }else
            {
                this._busy = false;
                this.playRaiseItem();
            }
        }

        private addToStage():void
        {
            let len = Math.min(this._waitsQuene.length,5) ;
            if(len <= 0) return ;
            let raiseData:IRaseData;
            let raiseItem:any;
            let url:string ;
            let index = 0 ;
            for(let i = 0 ; i < len ; i ++)
            {
                raiseData = this._waitsQuene.pop();
                url = this._registerDic[raiseData.key];
                raiseItem = zt.Pool.ins.getComFormPool(url);
                this._layer.addChild(raiseItem.displayObject);
                raiseItem.setData(raiseData);
                this._displayItems.push(raiseItem);
            }
           this._busy = true;
           this.arrange();
        }

        /**
         * 
         */
        public registerRaiseItem($key:string,url:string):void
        {
            this._registerDic[$key] = url;
        }


        private playRaiseItem():void
        {
            this.addToStage();
        }

        /**
         * item
         * @param url 
         */
        private getRaiseItem( url: string,isQuene:boolean = true): RaiseItem 
        {
            let raiseItem : RaiseItem = Laya.Pool.getItemByClass('raiseItem',RaiseItem);
            raiseItem.url = url ;
            raiseItem.isQuene = isQuene ;
            return raiseItem;
        }

        private oncomplete(raiseItem:any): void 
        {
            this._isFlying = false;
            let url = this._registerDic[raiseItem.dragItem.key];
            if(raiseItem.displayObject.parent&&raiseItem)
            {
                raiseItem.displayObject.parent.removeChild(raiseItem.displayObject);
            }
            zt.Pool.ins.returnToPool(raiseItem,url);
            const point = raiseItem.dragItem.raisePoint;
            const effectUrl:string = zt.utils.getResourceURL('effects/itemDrop/explode.json');
            const effect:Effect = zt.effects.createEffect(effectUrl,'gui',true);
            effect.playEffect(this._layer,point.x-35,point.y-35,false,Laya.BlendMode.ADD);
            this.flyItem();
          
        }

        static get instance(): RaisingItemManager 
        {
            return RaisingItemManager._instance || (RaisingItemManager._instance = new RaisingItemManager());
        }

        public init(layer: Sprite): void {
            this._layer = layer;
        }

    }

    export interface IRaseData
    {
        key:string;
        raisePoint:Point;
    }
}