namespace zt.universe
{
    /**
	 * @usage Rect
	 * @author wangcongyan 
	 * @since 2017.12.13
	 */
    export class Rect
    {
        public x:number = 0;
        public y:number = 0;
        public width:number = 0;
        public height:number = 0;
        private _right:number = 0;
        private _left:number = 0;
        private _bottom:number = 0;
        private _top:number = 0;
        constructor(x:number = 0,y:number=0,width:number=0,height:number=0)
        {
            this.x = x; 
            this.y = y;
            this.height = height ;
            this.width = width ;
        }

        public setTo(x:number = 0,y:number=0,width:number=0,height:number=0):void
        {
            this.x = x; 
            this.y = y;
            this.height = height ;
            this.width = width ;
            this._left = this.x;
            this._right = this.x + this.width ;
            this._top = this.y ;
            this._bottom = this.y + this.height ;
        }

        public get bottom():number
        {
            return this._bottom;
        }
        public get top():number
        {
            return this.y ;
        }
        public get left():number
        {
            return this.x ;
        }
        public get right():number
        {
            return this._right
        }

        public set bottom(value:number)
        {
           this._bottom = value ;
           this.height = this.bottom - this.top ;
        }
        public set top(value:number)
        {
            this._top = value ;
            this.y = value ;
            this.height = this.bottom - this.top ;
        }
        public set left(value:number)
        {
          this._left = value ;
          this.x = value ;
          this.width = this.right - this.left ;
        }
        public set right(value:number)
        {
           this._right = value ;
           this.width = this.right - this.left ;
        }

        /**
         * rect
         * @param rect 
         */
        public equal(rect:Rect):boolean
        {
            if(this.left == rect.left && this.right == rect.right 
            && this.top == rect.top && this.bottom == rect.bottom)
            {
                return true ;
            }
            return false ;
        }

        /**
         * target
         * @param target 
         */
        public cloneTo(target:Rect):void
        {
            target.setTo(this.x,this.y,this.width,this.height);
        }

        public scaleRatio($ratio:number = 1):Rect
        {
            const rect:Rect = new Rect();
            const x = Math.floor(this.x * $ratio );
            const y = Math.floor(this.y * $ratio );
            const width = Math.floor(this.width  );
            const height = Math.floor(this.height );
            rect.setTo(x,y,width,height)
            return rect;
        }

        public toString():string
        {   
            return this.x + ','+ this.y  + ',' + this.width  + ',' + this.height;
        }
    }

}