namespace zt.loader
{
	export class ModuleItem
	{
		private _url:string;
		private _name:string;
		private _projPrefix:string;
		private _onComplete:Laya.Handler;
		
		public load(moduleName:string,fileName:string, onComplete:Laya.Handler):void
		{
			this._name = moduleName;
			this._onComplete = onComplete;
			this._url = utils.getJSURL(fileName ? fileName:this._name);
			Laya.loader.load(this._url, Laya.Handler.create(this, this.onload), null, Laya.Loader.TEXT);
		}
		
		private onload(data:any):void {
			data += ' \n//# sourceURL=' + this._url;
			Laya.Browser.window.
			eval(data);
			Laya.Loader.clearRes(this._url);
			const obj:Object = utils.ModuleUtils.getInstance(this._name);
			if(this._onComplete) this._onComplete.runWith(obj);
		}
	}
}