namespace zt.effects
{	
	import Tween = Laya.Tween ;
	import Ease = Laya.Ease ;
	import Handler = Laya.Handler;
	import Sprite = Laya.Sprite;
	/**
	 * @class DamageNum
	 * @usage  
	 * @author congyan.wang
	 * @since 2016.08.26
	 */	
	export class DamageNum extends Sprite
	{
		private  _dispose:boolean = false;
		
		public  type:number = 0 ;
		
		public  isAdd:boolean = false;
		
		/**
		 *  
		 */		
		private static _damagePool:DamageNum[] = [];
		constructor()
		{
            super();
		}
		
		/**
		 *    
		 * @param $hurtNum 
		 * @param $type   01 2
		 * @param $isAdd
		 * @return 
		 */
		public static getDamageNum($hurtNum:number,$type:number,$isAdd:boolean):DamageNum
		{
			var damageNum:DamageNum = this._damagePool.pop() ;
			if(!damageNum)
			{
				damageNum = new DamageNum();
			}
			const hurtTxt:string = $hurtNum/1000 > 10000 ? $hurtNum%1000 + 'k':$hurtNum +'';
			damageNum.type = $type ;
			$isAdd = damageNum.isAdd = $hurtNum > 0 ? true : false ;
			DamageFactory.instance.getDamageView(damageNum,$hurtNum.toString(),$type,$isAdd);
			return damageNum ;
		}
		
		
		/**
		 *  
		 * @param $hurtNum
		 */		
		public setNum($hurtNum:number):void
		{
			DamageFactory.instance.getDamageView(this,$hurtNum.toString(),this.type,this.isAdd);
		}
		
		
		/**
		 *  
		 * @param scale
		 */		
		public setScale(scale:number):void
		{
			this.scaleX = this.scaleY = scale ;
		}
		
		
		/**
		 *  
		 */		
		public dispose():void
		{
			if(this.parent)
			{
				this.parent.removeChild(this);
			}
			this.graphics.clear();
			this.alpha = this.scaleY = this.scaleX = 1 ;
			this.x = this.y = 0 ;
			this._dispose = true ;
			DamageNum._damagePool.push(this);
		}

		/**
		 * 
		 * @param  
		 */
		public play($type:number):void
		{
			switch($type)
			{	
				case 0:
				case 1:
					this.playLoseBlood();
					break;
				case 2:
					this.playCrticle();
					break;
			}
		}

		private playLoseBlood():void
		{
			const radian = 100 ;
			var posX : Number = this.x + 100 * Math.cos(radian);
			var posY : Number = this.y + 100 * Math.sin(radian);
			this.setScale(1.5);
			Tween.to(this, {'alpha' : 1 ,'scaleX' : 1 , 'scaleY' : 1, 'x' : posX , 'y' : posY},1000,Ease.elasticOut,
			Handler.create(this,this.onPlayComplete));
		}


		private playCrticle():void
		{
			const radian = 100 ;
			var posX : Number = this.x + 100 * Math.cos(radian);
			var posY : Number = this.y + 100 * Math.sin(radian);
			this.setScale(1.5);
			Tween.to(this, {'alpha' : .5 ,'scaleX' : 1 , 'scaleY' : 1, 'x' : posX , 'y' : posY},1000,Ease.elasticOut,
			Handler.create(this,this.onPlayComplete));
		}

		/**
		 * 
		 */
		private onPlayComplete():void
		{
			this.graphics.clear();
			this.parent.removeChild(this);
			this.alpha = 1;
			DamageNum._damagePool.push(this);
		}
	}
	
}