namespace zt.app
{
    import Handler = Laya.Handler ;
    export class UIController implements IController
    {   
        protected _app:AppBase ;
        private _controllerName:string = 'moduleName';
        private _uiModel:IModel;
        protected _moduleName:string;
        private _prefix:string ;
        constructor(app:AppBase)
        {
            this._app = app ;
        }

    

        public init():void
        {
           
        }    
 
        public setModuleName(moduleName:string,$prefix:string):void
        {
            this._moduleName = moduleName ;
            this._prefix  = $prefix ? $prefix : '';
            this._controllerName = getNameByModuleAndprefix(this._moduleName,'controller',this._prefix);
        }

        public start():void
        {
         
        }

        public close() { this._app.startDispose(); }

        public dispose():void
        {
            throw new Error('dispose function must be override!');
        }

        public get model():IModel
        {
            if(!this._uiModel)
            {
                const moduleName = getNameByModuleAndprefix(this._moduleName,'model');
                this._uiModel =  Facade.getIns().retrieveModel(moduleName)
            }
            return this._uiModel ;
        }  

        public get prefix():string
        {
           return this._prefix;
        }

        public get moduleName():string
        {
            return this._moduleName ;
        }

        public get controllerlName():string 
		{	
           return this._controllerName;
        }

        public showSilder(silderName:string,handler:Handler):void
        {
            this._app.showSideBar(silderName,handler);
        }


        public hideSilder(silderName):void
        {
            this._app.closeSidebar();
        }
    }
}
