namespace zt.scene {
	import Sprite = Laya.Sprite;
	import ILayer = zt.universe.view.ILayer ;
	import Tween = Laya.Tween;
	export class SceneBase extends Sprite {
		private _moveTween:Tween;
		private static DURATION = 400;

		/**
		 * @param pos 
		 * @param layerName 
		 */
		move(pos: ScenePos, layerName: string, params) {
			const x = pos.x;
			const y = pos.y;
			const offsetX = pos.offsetX;
			const offsetY = pos.offsetY;
			const layer = this.layer ;
			const bgX = x ;
			const bgY = y ;
			let bgToX = Math.min(0, bgX + offsetX);
			let bgToY = Math.min(0, bgY + offsetY);
			bgToX = Math.max(bgToX, -  layer['width'] + Laya.stage.width);
			bgToY = Math.max(bgToY, - layer['height'] + Laya.stage.height);
			this._moveTween = Laya.Tween.to(layer, {
				x: bgToX,
				y: bgToY
			}, params.duration || SceneBase.DURATION, null, Laya.Handler.create(this, this.moveComplete));
		}

		private find(layerName: string): ILayer {
			return this.layers.filter((layer) => {
				return layer.name === layerName;
			})[0];
		}

		public update(val: any) {
			this.params = val;
			this.reBuild();
		}

		public preproccess(): void 
		{
			 throw Error('must be overrided') 
		}
		public build(): void 
		{
			 throw Error('must be overrided') 
		}
		public dispose() 
		{
			if(this._moveTween)
			{
				this._moveTween.clear();
				this._moveTween = null; 
			}
			this.disposeTexture();
		}

		/**
		 * 
		 */
		private disposeTexture():void
		{
			const res:any = this.res;
			let url:string = ''
			for(let urlObj of res)
			{	
				if(!!urlObj.ignore)continue ;
				url = urlObj.url ;
				Laya.loader.clearRes(url,true);
			}
		}

		protected moveComplete() { throw Error('must be overrided') }

		get cmds(): { cmd: string, params, data?}[] { return [] }
		get res(): Array<any> { return null }
		protected _params: SceneParams;
		set params(val: any) { this._params = val; }
		get params() { return this._params }
		protected reBuild() { throw Error('must be overrided') }
		protected get layer(): ILayer { return null; }
		protected get layers(): ILayer[] { return null; }
	}
}