namespace zt.tips {
	import Sprite = Laya.Sprite;
	export class BaseTip {
		protected _data: any;
		protected _bgWidth: number;
		protected _bgHeight: number;
		protected _ui: Sprite;

		setData(data: any): void {
			this._data = data;
		}

		get bgWidth(): number { return this._bgWidth }
		get bgHeight(): number { return this._bgHeight }
		draw(): void { throw Error('must be overrided') }

		dispose(): void {
			this._ui.removeSelf();
		}

		get ui(): Sprite { return this._ui }
	}
}