namespace zt.tips {
	export class CommonTip extends BaseTip {
		draw() {
			const ui = (this._data as UIBase).ui;
			this._bgWidth = ui.getChild('bg').displayObject.width;
			this._bgHeight = ui.getChild('bg').displayObject.height;
			this._ui = ui.displayObject;
		}

		dispose() {
			super.dispose();
			(<UIBase>this._data).dispose();
		}
	}
}