namespace zt {
	export class ObjectUtils {
		
		static hasKey(obj:object):boolean {
			if(!obj)return false;
			let bol = false;
			if(obj == null || typeof obj.constructor == 'string')
				return bol;
			for(const key in obj) {
				bol = true;
				break;
			}
			return bol;
		}
	}

	export const getKey = (object) => Object.keys(object)[0];
	export const getValue = (object) => object[getKey(object)];
}