namespace zt.tips {
	import Sprite = Laya.Sprite;
	export class NormalTip extends BaseTip {
		private static MAX_TEXT_WIDTH = 300;
		private static GAP = 10;
		private static LINE_GAP = 5;
		private _bg: Sprite;
		private _list: Laya.Text[];
		private _h: number;

		constructor() {
			super();
			this._list = [];
			this._ui = new Sprite;
		}

		draw(): void {
			if (this._bg)
				this._bg.removeSelf();
			this._bg = getTipBg();
			this._ui.addChildren(this._bg);
			this._list = [];
			this._bgWidth = 0;
			this._h = 0;
			this._h += NormalTip.GAP;

			for (const item of this._data) {
				this._list.push(this.createText(item));
			}

			this._bgHeight = (this._h + NormalTip.GAP - NormalTip.LINE_GAP);

			this._bg.width = this._bgWidth;
			this._bg.height = this._bgHeight;
		}

		private createText(text: string): Laya.Text {
			const item: Laya.Text = new Laya.Text();
			item.wordWrap = true;
			item.text = text;
			item.color = '#FFFFFF';
			item.overflow = Laya.Text.VISIBLE;
			// tslint:disable-next-line:no-magic-numbers
			item.fontSize = 14;
			item.width = NormalTip.MAX_TEXT_WIDTH;
			item.x = NormalTip.GAP;
			item.y = this._h;
			let bgWidth: number = NormalTip.MAX_TEXT_WIDTH + NormalTip.GAP * 2;
			if (item.textWidth < NormalTip.MAX_TEXT_WIDTH)
				bgWidth = item.textWidth + NormalTip.GAP * 2;
			if (bgWidth > this._bgWidth)
				this._bgWidth = bgWidth;
			this._h += item.textHeight + NormalTip.LINE_GAP;
			this._ui.addChildren(item);
			return item;
		}

		dispose() {
			super.dispose();
			this._list.forEach(i => i.removeSelf());
		}
	}
}