namespace zt.app {
	import Sprite = Laya.Sprite;
	import Event = Laya.Event;
	import Mouse = Laya.Mouse;
	import Rectangle = Laya.Rectangle;
	import Handler = Laya.Handler;
	import Loader = Laya.Loader;
	export class AppBase extends Sprite {
		protected _params: AppParams;
		xpos: number;
		ypos: number;
		private _level;//
		private _sidebar: AppSidebar;//

		/**
		 *  
		 */
		protected _adaptionType:number = AdaptationType.ADAPT_CENTER ;
		/**
		 * 
		 */
		public moduleName:string = '';
		/**
		 * @param  {number} $level app.AppLevel
		 */
		constructor($level: number = app.AppLevel.LEVEL_1) {
			super();
			this.setLevel($level);
			this.initEvent();
		}

		init(): void {
			throw new Error('method "init" must be overrided')
		}

		start(): void {
			Facade.getIns().startModule(this.moduleName);
			if(this.level == AppLevel.LEVEL_0)
			{
				(zt.Layers.getLayer('gui') as LayerNode).hide() ;
				(zt.Layers.getLayer('scene') as LayerNode).hide() ;
			}
		}

		protected initEvent(): void {
	
		}

		/**
		 * 
		 * @param $iview:IView
		 */
		protected superRegisterView($iView:IView,$prefix=''):IView
		{
			$iView.setModuleName(this.moduleName,$prefix);
			Facade.getIns().registerUIView($iView);
			return $iView;
		}
		
		/**
		 * 
		 * @param  $iMode:IMode
		 */
		protected superRegisterModel($iModel:IModel,$prefix=''):IModel
		{
			const imodel = $iModel.setModuleName(this.moduleName,$prefix);
			Facade.getIns().registerModel($iModel);
			return $iModel
		}

		protected superRegisterController($icontroller:IController,$prefix=''):IController
		{
			$icontroller.setModuleName(this.moduleName,$prefix);
			Facade.getIns().registerController($icontroller);
			return $icontroller;
		}

		/**
		 *  
		 */
		getAppBounds(): Rectangle {
			return this.getBounds()
		}

		/**
		 *  
		 */
		adaptation():void
		{
			const handler = this._adaptionType == AdaptationType.ADAPT_FULL_SCREEN ? Handler.create(this,this.resize,null,false):null;
			Adaptation.register(this,this._adaptionType,handler);
		}


		/**
		 * 
		 */
		public resize():void
		{
			zt.ctrl.GameCtrl.intance.event('REQUEST_GUIDE');
		}

		/** */
		showSideBar(name: string, param?: any): void {
			if (!this.isShowSidebar(name)) {
				this._sidebar.mc.removeSelf();
				this._sidebar.showSideBar(name, param);
				const stageW = Laya.stage.width;
				const mainW = this.getAppBounds().width;
				const sidebarW = this._sidebar.mc.getBounds().width;
				this._sidebar.mc.x = mainW;
				this.addChild(this._sidebar.mc);
				this.xpos = stageW / 2 - (mainW + sidebarW) / 2;
				Laya.Tween.to(this, {
					x: this.xpos
				}, 100);
			}
			else {
				this._sidebar.showSideBar(name, param);
			}
		}

		closeSidebar(): void {
			this._sidebar.clear();
		}

		/**
		 * 
		 * @param name 
		 */
		isShowSidebar(name: string): boolean {
			return this._sidebar.currName == name;
		}

		/**
		 * 
		 * @param name 
		 */
		getSidebarView(name: string): any {
			return this._sidebar.getView(name);
		}

		createSidebar(arr: any[]): void {
			if (this._sidebar == null) {
				this._sidebar = new AppSidebar(arr);
				this.addChild(this._sidebar.mc);
			}
		}

		public setSidebarRenderer(name: string, renderer: Laya.Handler): void {
			this._sidebar.setSidebarRenderer(name, renderer);
		}

		get sidebarList(): any[] {
			return [];
		}

		private onMouseDown(e: Event): void {
			e.stopPropagation();
			if ((e.target as any) != this.dragRect) return;
			this.startDrag();
			Mouse.cursor = 'pointer';
		}

		private onMouseUp(e: Event): void {
			e.stopPropagation();
			this.stopDrag();
			Mouse.cursor = 'default';
		}

		protected load(): void {
			throw new Error('load method must be overrided')
		}

		get res(): any[] {
			throw new Error('res method must be overrided')
		}
		get cmds(): { cmd: string, params, data?}[] { return [] }

		protected get dragRect(): Sprite {
			return null;
		}

		startDispose(): void {
			this.event(AppEvent.START_DISPOSE);
		}

		public set params(val: any) {
			this._params = val;
		}

		public get level(): number {
			return this._level;
		}

		private setLevel($level: number): void {
			this._level = $level;
		}

		dispose(): void {
			if (this._sidebar != null) {
				this._sidebar.dispose();
			}
			if (this._params && this._params.onClose) {
				this._params.onClose.run();
			}
		
			Adaptation.unregister(this,this._adaptionType);
			zt.effects.disposeModule(this.moduleName);
			this._sidebar = null;
			this.removeSelf();
			this.disposeTexture();
			Facade.getIns().removeModule(this.moduleName);
			zt.ctrl.GameCtrl.intance.event('AppClose',this.moduleName);
			if(this.level == AppLevel.LEVEL_0)
			{
				(zt.Layers.getLayer('gui') as LayerNode).show() ;
				(zt.Layers.getLayer('scene') as LayerNode).show() ;
			}
		}

		public get adaptionType():number
		{
			return this._adaptionType;
		}

		/**
		 * 
		 */
		disposeTexture():void
		{
			const res:any = this.res;
			let url:string = ''
			for(let urlObj of res)
			{
				url = urlObj.url ;
				Laya.loader.clearRes(url,true);
			}
		}
	}
}