namespace zt.universe
{   
	/**
	 * @usage 
	 * @author wangcongyan 
	 * @since 2017.12.13
	 */
    export class UniverseData
    {   
        /***/
        public W_SIZE:number = 400 ;
        public H_SIZE:number = 400 ;
        private _universeWidth:number ;
		private _universeHeight:number ;
		private _mapWidth:number ;
        private _mapHeight:number ;
        private mapShowR:number;
        private mapShowC:number;
        private tPaddingRect:Rect;
		private _gridHash:object ;
		private _baseHash:object ;
        private _viewRec:Rect ;
        private _dragRec:Rect;
        private _mapLastRect:Rect;
		private _mapRect:Rect;
		private _bgLayerRect:Rect;
        private _centerX:number;
        private _centerY:number;
		private _elementHash:object;
		private _bgRatio:number = .5 ;
		private _moveRatio:number = 0 ;
		private _sceneID:number ;
		/***/
		private _isEqualLast:boolean ;
        constructor()
        {
			
        }

        /**
         * 
         */
        public initUniverse(sceneData:ISceneVo,$elementList:any[]):void
        {
			this.clear();
			this._sceneID = sceneData.SceneID ;
            this._universeWidth = sceneData.Xsize;
			this._universeHeight = sceneData.Ysize
			this._mapWidth = sceneData.mapW ;
			this._mapHeight = sceneData.mapH ;
			this._bgRatio = this._mapWidth/this.universeWidth ;
			this.initGrid();
			this.initBase();
			this.initElementList($elementList);
        }

        /**
		 *  
		 */		
		private initGrid():void
		{
            const swidth = this._universeWidth ;
            const sheight = this._universeHeight ;
            const width = this.W_SIZE;
            const height = this.H_SIZE ;
			let rNum: number = Math.ceil(swidth/height);
			let cNum: number = Math.ceil(swidth/width);
			let gridVo:GridVo;
			let rec:Rect;
			for(let i: number=0;i<rNum;i++)
			{
				for(let j: number=0;j<cNum;j++)
				{
					gridVo=new GridVo();
					rec= this.getGridRec(i,j);
					gridVo.rIndex=i;
					gridVo.cIndex=j;
					gridVo.width=rec.width;
					gridVo.height=rec.height;
					gridVo.x=rec.x;
					gridVo.y=rec.y;
					this._gridHash[gridVo.key]=gridVo;
				}
			}
			this._viewRec.width = Laya.stage.width ;
			this._viewRec.height = Laya.stage.height ;
        }       
        
        
        	/**
		 *  
		 * @param $d
		 */		
		private initElementList(elementList:IElementVo[]):void
		{
			for(let elementVo of elementList)
            {   
                this.registerData(elementVo);
			}
			this.onResize();
        }
        

        /***/
		private initBase():void
		{
            const gridW:number = this.W_SIZE ;
            const gridH:number = this.H_SIZE ;
			const rNum:number = Math.ceil(this._mapWidth/gridW);
			const cNum:number = Math.ceil(this._mapHeight/gridH);
            let index:number = 0 ;
			let baseVo:BaseVo;
			let index_r:number = 0;
			for(var j:number=0;j<cNum;j++)
			{
				for(var i:number=0;i<rNum;i++)
				{
					baseVo=new BaseVo();
					index_r = j * rNum + i ;
					baseVo.rIndex=i;
					baseVo.cIndex=j;
					baseVo.X = i * gridW ;
					baseVo.Y = j * gridH ;
					baseVo.index = index ;
					this._baseHash[index] = baseVo;
					this._baseHash[baseVo.key] = baseVo;
					index ++ ;
				}
			}
		}


        public  getGridRec(r:number,c:number):Rect
		{
            const swidth = this._universeWidth ;
            const sheight = this._universeHeight ;
            const width = this.W_SIZE;
            const height = this.H_SIZE ;
			let cNum:number = Math.ceil(swidth/width);
			let rNum:number = Math.ceil(sheight/height);
			let rec:Rect=new Rect(c*width,r*height,width,height);
			if(c==cNum-1)
			{
				rec.width= swidth-rec.x;
			}
			if(r==rNum-1)
			{
				rec.height= sheight-rec.y;
			}
			return rec;
        }

		/**
		 * 
		 * @param c 
		 * @param r 
		 */
        private hideGrid(c:number,r:number):void
        {
            var $gridVo:GridVo = this.getGridVo(c,r); 
			if(!$gridVo)return ;
			var showList:any[] = $gridVo.elementKeyList ;
			if(showList.length == 0)return ;
			$gridVo.isOnShow = false;
			ctrl.GameCtrl.intance.event('HIDE_ELEMENT',showList);
        }

		/**
		 * 
		 * @param c 
		 * @param r 
		 */
        private showGrid(c:number,r:number):void
        {
            var $gridVo:GridVo = this.getGridVo(c,r); 
			if(!$gridVo)return ;
			var showList:any[] = $gridVo.elementKeyList ;
			if(showList.length == 0)return ;
			$gridVo.isOnShow = true;
			ctrl.GameCtrl.intance.event('SHOW_ELEMENT',showList);
        }

         /**
		 * 
		 * @param	moveX x
		 * @param	moveY y
		 * @param   $isForce 
		 */
		public moveViewPort(moveX:number, moveY:number,$isForce:boolean = false):void {
			this._viewRec.x = moveX;
			this._viewRec.y = moveY;
			this.updateViewPort($isForce);
			
        }

        /**
		 *  
		 * @param $isForceRefresh
		 */	
        public updateViewPort($isForceRefresh:boolean = false):void 
        {
			if($isForceRefresh){
				this.hideAll();
			}
            const viewRect:Rect = this._viewRec ;
            const width = this._universeWidth ;
            const height = this._universeHeight ;
            const gwidht = this.W_SIZE;
            const gheight = this.H_SIZE;
			this._centerX = viewRect.x + viewRect.width ;
			this._centerY = viewRect.y + viewRect.height ;
			let viewPortX = this._centerX - viewRect.width;
			let viewPortY = this._centerY - viewRect.height ;
			var tRight:Number = viewPortX + viewRect.width;
			if (tRight > width)
			{
				viewPortX = width - viewRect.width;
			}
			var tBottom:Number = viewPortY + viewRect.height;
			if (tBottom > height)
			{
				viewPortY = height - viewRect.height;
			}
			if (viewPortX < 0)
			{
				viewPortX = 0;
			}
			if (viewPortY < 0)
			{
				viewPortY = 0;
			}
			var rnum:number = Math.ceil(width/gwidht);
			var cnum:number = Math.ceil(height/gheight);
			this.tPaddingRect.setTo(200,200,300,300);
			const bottom : number = Math.ceil((viewPortY + this._viewRec.height  + this.tPaddingRect.y) / this.H_SIZE);
			const top : number = Math.floor((viewPortY - this.tPaddingRect.y) / this.W_SIZE);
			const left : number = Math.floor((viewPortX - this.tPaddingRect.x) / this.W_SIZE) ;
			const right : number =  Math.ceil((viewPortX +this._viewRec.width + this.tPaddingRect.x ) /this.W_SIZE) ;
			this._mapRect.top = Math.max(top,0);
			this._mapRect.left = Math.max(left,0);
			this._mapRect.bottom = Math.min(bottom,cnum);
			this._mapRect.right = Math.min(right,rnum);
			let lastLeft = this._mapLastRect.left;
			let lastRight = this._mapLastRect.right;
			if(lastLeft == 0 && lastRight == 0  || $isForceRefresh)
			{
				this._mapLastRect.setTo(left,top,0,0);
			}
			const equal = this._mapRect.equal(this._mapLastRect)
			this._isEqualLast = this._mapRect.equal(this._bgLayerRect) ;
			// console.log('lastMapRect : ' + this._mapLastRect.toString());
			// console.log('mapRect : ' + this._mapRect.toString());
			if(equal && $isForceRefresh == false)return ;
			this.clipViewPort();
			this._mapLastRect.top = this._mapRect.top;
			this._mapLastRect.bottom = this._mapRect.bottom;
			this._mapLastRect.left = this._mapRect.left;
			this._mapLastRect.right = this._mapRect.right;
			// this.outElementList();
			
		}
		
		/**
		 * 
		 */
		public outElementList():void
		{
			let gridvo:GridVo ;
			let resutlt:IElementVo[] = [];
			for(let key in this._gridHash)
			{
				gridvo = this._gridHash[key];
				if(gridvo.isOnShow)
				{
					resutlt = resutlt.concat(gridvo.elementKeyList);
				}
			}
			console.log(resutlt);
		}
		

        /**
		 * 
		 * @param	width		
		 * @param	height		
		 * @param	rect		
		 * @return
		 */
		public changeViewPortBySize(width:number, height:number,rect:Rect = null):Rect
		{
			if (rect == null)
			{
				rect = new Rect();
			}
			this._centerX = this._viewRec.x + this._viewRec.width ;
			this._centerY = this._viewRec.y + this._viewRec.height;
			rect.x = this._centerX - width ;
			rect.y = this._centerY - height ;
			rect.width = width;
			rect.height = height;
			this.changeViewPort(rect.x, rect.y, rect.width, rect.height);
			return rect;
        }
		
		/**
		 * 
		 */
		private hideAll():void
		{
			let gridvo:GridVo ;
			let showList:IElementVo[] = [];
			for(let key in this._gridHash)
			{
				gridvo = this._gridHash[key];
				if(gridvo.isOnShow)
				{
					showList = showList.concat(gridvo.elementKeyList);
					gridvo.isOnShow = false; 
				}
			}
			ctrl.GameCtrl.intance.event('HIDE_ELEMENT',showList);
		}

        /**
		 * 
		 * @param	moveX	x
		 * @param	moveY	y
		 * @param	width	
		 * @param	height	
		 */
		public changeViewPort(moveX:number, moveY:number, width:number, height:number):void {
			this._viewRec.x = moveX;
			this._viewRec.y = moveY;
			this._viewRec.width = width;
			this._viewRec.height = height;
			this.updateViewPort();
		}


        /**
		 * GRID
		 */
		private clipViewPort():void {
			let tSpriteNum:number = 0;
			let tIndex:number = 0;
			let tSub:number = 0;
			let tAdd:number = 0;
            let i:number, j:number;
            const mapRect = this._mapRect;
			const mapLastRect = this._mapLastRect ;
			if (mapRect.left > mapLastRect.left) {
				//
				tSub = mapRect.left - mapLastRect.left;
				if (tSub > 0) {
					for (j = mapLastRect.left; j < mapLastRect.left + tSub; j++) {
						for (i = mapLastRect.top; i <= mapLastRect.bottom; i++) {
							this.hideGrid(j, i);
						}
					}
				}
			} else {
				//
				tAdd = mapLastRect.left - mapRect.left;
				if (tAdd > 0) {
					for (j = mapRect.left; j < mapRect.left + tAdd; j++) {
						for (i = mapRect.top; i <= mapRect.bottom; i++) {
							this.showGrid(j, i);
						}
					}
				}
			}
			if (mapRect.right > mapLastRect.right) {
				//
				tAdd = mapRect.right - mapLastRect.right;
				if (tAdd > 0) {
					for (j = mapLastRect.right ; j <= mapLastRect.right + tAdd; j++) {
						for (i = mapRect.top; i <= mapRect.bottom; i++) {
							this.showGrid(j, i);
						}
					}
				}
			} else {
				//
				tSub = mapLastRect.right - mapRect.right
				if (tSub > 0) {
					for (j = mapRect.right + 1; j <= mapRect.right + tSub; j++) {
						for (i = mapLastRect.top; i <= mapLastRect.bottom; i++) {
							this.hideGrid(j, i);
						}
					}
				}
			}
			if (mapRect.top > mapLastRect.top) {
				//
				tSub = mapRect.top - mapLastRect.top;
				if (tSub > 0) {
					for (i = mapLastRect.top; i < mapLastRect.top + tSub; i++) {
						for (j = mapLastRect.left; j <= mapLastRect.right; j++) {
							this.hideGrid(j, i);
						}
					}
				}
				
			} else {
				//
				tAdd = mapLastRect.top - mapRect.top;
				if (tAdd > 0) {
					for (i = mapRect.top; i < mapRect.top + tAdd; i++) {
						for (j = mapRect.left; j <= mapRect.right; j++) {
							this.showGrid(j, i);
						}
					}
				}
				
			}
			if (mapRect.bottom > mapLastRect.bottom) {
				//
				tAdd = mapRect.bottom - mapLastRect.bottom;
				if (tAdd > 0) {
					for (i = mapLastRect.bottom + 1; i <= mapLastRect.bottom + tAdd; i++) {
						for (j = mapRect.left; j <= mapRect.right; j++) {
							this.showGrid(j, i);
						}
					}
				}
			} else {
				//
				tSub = mapLastRect.bottom - mapRect.bottom
				if (tSub > 0) {
					for (i = mapRect.bottom + 1; i <= mapRect.bottom + tSub; i++) {
						for (j = mapLastRect.left; j <= mapLastRect.right; j++) {
							this.hideGrid(j, i);
						}
					}
				}
			}
        }
        
        /**
		 *  
		 * @param iGrid
		 */		
		public registerData(element:IElementVo):void
		{
            const cIndex =  element.X/this.W_SIZE >> 0;
            const rIndex = element.Y/this.H_SIZE >> 0;
			var gridVo:GridVo= this.getGridVo(cIndex,rIndex);
			if(gridVo)
			{
				gridVo.registerKey(element);
			}
        }
        
        public getGridVo(r:number,c:number):GridVo
		{
			return this._gridHash[r+","+c];
		}

		public getBaseVo(r:number,c:number):BaseVo
		{
			return this._baseHash[r+","+c];
		}


		public get bgRatio():number
		{
			return this._bgRatio;
		}

		public get moveRatio():number
		{
			return this._moveRatio;
		}

		/**
		 * ID
		 */
		public get sceneID():number
		{
			return this._sceneID ;
		}
		
		/**
		 * 
		 */
		public get universeWidth():number
		{
			return this._universeWidth ;
		}

		/**
		 * 
		 */
		public get universeHeight():number
		{
			return this._universeHeight ;
		}

		public get isEqualLast():boolean
		{
			return this._isEqualLast;
		}

		public get mapWidth():number
		{
			return this._mapWidth ;
		}

		public get mapHeight():number
		{
			return this._mapHeight;
		}

		public get viewRec():Rect
		{
			return this._viewRec;
		}

		/**
		 * 
		 */
		public get containData():IElementVo[]
		{
			const mapRect:Rect = this._mapRect ;
			let containData = [];
			let gridVo:GridVo ;
			for(let i =  mapRect.left ; i < mapRect.right ; i ++)
			{
				for(let j =  mapRect.top ; j < mapRect.bottom ; j ++)
				{
					gridVo = this.getGridVo(i,j);
					containData = containData.concat(gridVo.elementKeyList);
				}
			}
			return containData;
		}

		public getByRect($x:number,$y:number):number[]
		{
			const baseList:number[] = [];
			let left , right ,top ,bottom ;
			const gridW:number = this.W_SIZE ;
			const gridH:number = this.H_SIZE ;
			const rNum:number = Math.ceil(this._mapWidth/gridW);
			const cNum:number = Math.ceil(this._mapHeight/gridH);
			if(this._moveRatio == 1)
			{
				left = this._mapRect.left  ;
				top =  this._mapRect.top ;
				bottom = this._mapRect.bottom ;
				right = this._mapRect.right ;
			}else
			{
				 left = Math.abs(Math.floor($x / gridW)) -1;
				 top =  Math.abs(Math.floor($y / gridH)) -1;
				 bottom = top + this._mapRect.height ;
				 right = left + this._mapRect.width ;
			}	
			left = Math.max(0,left);
			top = Math.max(0,top);
			right = Math.min(right ,rNum);
			bottom = Math.min(bottom ,cNum);
			this._bgLayerRect.setTo(left,top,this._mapRect.width,this._mapRect.height);
			for(let i  = left ; i < right ; i ++)
			{	
				for(let j  = top ; j < bottom ; j ++)
				{
					baseList.push( j * rNum + i);
				}
			}
			return baseList ;
		}

		/**
		 * 
		 * @param  
		 */
		public getBaseByIndex($index:number):BaseVo
		{
			return this._baseHash[$index];
		}
        
        public onResize():void
		{
			const swidth = this._universeWidth ;
            const sheight = this._universeHeight ;
            const width = this.W_SIZE;
            const height = this.H_SIZE ;
			const stageW = this._viewRec.width = Laya.stage.width;
			this._viewRec.height=Laya.stage.height;
			this._dragRec.x=this._viewRec.width-swidth;
			this._dragRec.y=this._viewRec.height-sheight;
			this._dragRec.width=-this._viewRec.width+swidth;
			this._dragRec.height=-this._viewRec.height+swidth;
			this.mapShowR=Math.floor(Laya.stage.height/width)+2;
			this.mapShowC=Math.floor(Laya.stage.width/height)+2;
			if(swidth == this._mapWidth) 
				this._moveRatio  = 1 ;
			else
				this._moveRatio = (this._mapWidth - stageW) / (swidth -stageW);
		}

		private clear():void
		{
			this._gridHash = {};
            this._elementHash = {} ;
            this._dragRec = new Rect();
			this._mapRect = new Rect();
			this._viewRec = new Rect();
			this._bgLayerRect = new Rect();
			this._baseHash = {};
			this.tPaddingRect = new Rect();
			this._mapLastRect = new Rect();
		}

		/**
		 * 
		 */
		public dispose():void
		{
			this._gridHash = null;
            this._elementHash = null;
            this._dragRec = null;
            this._viewRec = null;
            this.tPaddingRect = null;
		}
	}
	export interface ISceneVo
	{	
		Xsize:number;
		Ysize:number;
		mapW:number;
		mapH:number;
		SceneID:number;
	}
}