// tslint:disable:no-magic-numbers
namespace zt {
    import Text = Laya.Text;
    import Sprite = Laya.Sprite;
    export class AppLoading extends Laya.Sprite {
        private _free: boolean;
        private _mask: Sprite;
        private _ui: fairygui.GProgressBar;
        constructor() {
            super();
            this.initEvent();
        }

        private initEvent() {
            //
        }

        draw() {
            this._mask = new Sprite();
            this.addChild(this._mask);
            this._mask.graphics.drawRect(0, 0, Laya.stage.width, Laya.stage.height, '#000000');
            this.size(Laya.stage.width, Laya.stage.height);
            this._mask.alpha = 0.5;
            this._mask.mouseEnabled = true;
            this._ui = fairygui.UIPackage.createObject('lib', 'apploading') as fairygui.GProgressBar;
            this._ui.update(20);
            this._ui.displayObject.pivot(this._ui.displayObject.width / 2, this._ui.displayObject.height / 2);
            this.addChild(this._ui.displayObject);
            this._ui.displayObject.x = Laya.stage.width / 2;
            this._ui.displayObject.y = Laya.stage.height / 2;
        }

        set free(val: boolean) {
            this._free = val;
        }

        dispose() {
            this.free = true;
        }

        private static _instance: AppLoading;
        show() {
            this.draw();
            Laya.stage.addChild(this);
        }

        hide() {
            this.removeChildren();
            this.removeSelf();
            this._mask = null;
            this._ui = null;
        }

        update(val: number) {
            this._ui && this._ui.update(val * 100 * 0.8 + 20);
        }

        static get instance(): AppLoading {
            return AppLoading._instance || (AppLoading._instance = new AppLoading());
        }
    }

    export const showAppLoading = () => {
        AppLoading.instance.show();
    }

    export const hideAppLoading = () => {
        AppLoading.instance.hide();
    }

    export const updateAppLoading = (val: number) => {
        AppLoading.instance.update(val);
    }
}