namespace zt
{
    import Sprite = Laya.Sprite ;
    import Texture = Laya.Texture;
    
    /**
     * @usage 
     * @since 2018.01.10
     * @author wangcongyan
     */
    export class PathTarget extends Sprite
    {
        private pathPointUrl:string;
        constructor()
        {
            super();
            this.pathPointUrl  = zt.utils.getResourceURL('universe/line/point.png');
            this.loadTex();
        }

        private loadTex():void
        {
            const pt:Texture =  Laya.loader.getRes(this.pathPointUrl);
            this.graphics.drawTexture(pt,-32,-24);
        }

        public show():void
        {
            this.visible = true;
        }

        public hide():void
        {
            this.visible = false;
        }

        public dispose():void
        {
            this.graphics.clear();
        }
    }
}
