namespace zt.ctrl  {

	import EventDispatcher = Laya.EventDispatcher;
	/**
	 * 
	 */
	export class GameCtrl extends EventDispatcher  {
		private static mInstance:GameCtrl;		
		static get intance():GameCtrl{
			if(GameCtrl.mInstance) return GameCtrl.mInstance;
			GameCtrl.mInstance = new GameCtrl();
			return GameCtrl.mInstance;
		}
	}
}