namespace zt.utils {

    export class ModuleUtils {
        /**
         * 
         * @param name module name
         */
        static getModulePath(name: string): string {
            return 'zt.' + name + '.' + name;
        }

        /**
         * 
         * @param name 
         */
        static hasDefinition(name: string): Boolean {
            return !!ModuleUtils.getModuleDefinition(name);
        }

        /**
         * 
         * @param name 
         */
        static getModuleDefinition(name: string): Function {
            const classpath: string = ModuleUtils.getModulePath(name);
            return getDefinition(classpath);
        }

        /**
         * 
         * @param name 
         */
        static getInstance(name: string): Object {
            const cls: any = ModuleUtils.getModuleDefinition(name);
            return new cls();
        }
        
    }
}