namespace zt.utils
{
    import Point = Laya.Point ;
        /**
         * 
         */
        export const getAngle = (form: any, to: any): number =>
        {
            const lx = to.x - form.x;
            const ly = to.y - form.y;
            return Math.atan2(ly, lx) * 180 / Math.PI;
        }

        /**
         * 
         */
        export const getDistance=(form: any, to: any): number=> {
            const p1 = new Laya.Point(form.x, form.y);
            return p1.distance(to.x, to.y);
        }

        /**
         *  1.00
         * @param value 
         * @param fixnum 
         */
        export const fix = (value : number,fixnum = 0):number=>
        {
            const fixNum = fixnum + 1; 
            const power = Math.pow(10,fixNum);
            const num = Math.round( value * power)/power;
            return num;
        }

        export const sum = (nums: number[]) => {
            if (nums.length == 0) return 0;
            if (nums.length == 1) return Number(nums[0]);
            return Number(nums.reduce((a, b) => a + b));
        }

        /**
         * 
         * @param  
         */
        export const getTargetToCenter = (x:number,y:number,p1?:Point,p2?:Point)=>
        {   
            // const p1 = new Point(475,65);
            // const p2 = new Point(1437,1015);
            const A = 950;
            const B = -962
            const C = -388720 ;
            const length =  Math.abs(A * x + B * y + C)/ Math.sqrt(A*A + B*B);
            return length ;
        }

        /**
         * 
         * @param  
         */
        export const getLineVerticalPoint = (x:number,y:number,p1?:Point,p2?:Point):Point=>
        {   
            p1 = new Point(475,65);
            p2 = new Point(1437,1015);
            const A = (p1.y-p2.y)/(p1.x- p2.x);  
            const B = p1.y-A*p1.x;
            const m =  x + A*y;  
            const px = (m-A*B)/(A*A + 1);  
            const py = px+B;  
            return new Point(px,py) ;
        }

        
}