// tslint:disable:no-console
namespace zt.net {
	import EventDispatcher = Laya.EventDispatcher;
	import Handler = Laya.Handler;
	import Event = Laya.Event;
	import Byte = Laya.Byte;
	export class GameSocket extends EventDispatcher {

		private static _instance: GameSocket;
		private _socket: ISocketProxy;
		private _onConnected: Handler;
		private _url: string;
		private _counter: number;
		private _callbacks: object;
		private _errorHandlers: object;
		private _componentMsgCb: Handler;
		private _componentErrorCb: Handler;
		private _inited: boolean;
		private protocols: Array<any>;

		constructor() {
			super();
		}

		static get instance(): GameSocket {
			return GameSocket._instance || (GameSocket._instance = new GameSocket());
		}

		init(socket: ISocketProxy): void {
			if (this._inited) return;
			this._inited = true;
			this._socket = socket;
			this._counter = 0;
			this._callbacks = {};
			this._errorHandlers = {};
			this.initEvent();
		}

		connect(url: string, onConnected: Handler): void {
			this._url = url.indexOf('ws://') == -1 ? 'ws://' + url : url;
			this._onConnected = onConnected;
			this._socket.connectByUrl(this._url);
		}

		setProtocol(protocol: string): GameSocket {
			this._socket.protocols.push(protocol);
			return this;
		}

		sendHeart(): void {
			const msgHead: MessageHeadOut = new MessageHeadOut(MessageType.NULL);
			const out: PackageOut = new PackageOut();
			out.writeData(msgHead);
			this._socket.send(out.buffer);
			console.log(`[${this.getTime()}][sending heart]`, 'head', msgHead.toString());
		}

		sendComponentMsg(msgHead: MessageHeadOut, componentCMD: number, data: any, callback: Handler, errorHandler: Handler): void {
			this._componentMsgCb = callback;
			this._componentErrorCb = errorHandler;
			const out: PackageOut = new PackageOut();
			out.writeData(msgHead, componentCMD, data);
			this._socket.send(out.buffer);
			const compression: String = out.flag == FlagType.COMPRESS ? 'compressed' : 'uncompressed';
			const compresslen: String = out.flag == FlagType.COMPRESS ? ',compresslen=' + out.msgBodyLen : '';
			__getDebugEnabled('bufferlen') && console.log(`[buffer len =  ${out.buffer.byteLength}]`)
			console.log(`[${this.getTime()}][sending component message][` + compression + ',msglen=' + out.msgbody.length + '' + compresslen + ']',
				msgHead.toString(), 'component cmd:', componentCMD, 'data:', data);
		}

		/**
		 * send 
		 * @param msgHead
		 * @param data
		 * @param callback
		 * @param errorHandler
		 * 
		 */
		send(msgHead: MessageHeadOut, data: any, callback: Handler, errorHandler: Handler): void {
			if (!zt.__getEnabled('net')) return;
			if (!this._callbacks)
				throw new Error('game socket.');
			this._counter++;
			this._callbacks[this._counter] = callback;
			this._errorHandlers[this._counter] = errorHandler;
			data['Seq'] = this._counter;
			data = JSON.stringify(data);
			const out: PackageOut = new PackageOut();
			out.writeData(msgHead, 0, data);
			this._socket.send(out.buffer);
			__getDebugEnabled('bufferlen') && console.log(`[buffer len =  ${out.buffer.byteLength}]`)
			console.log(`[${this.getTime()}][sending][Seq=${this._counter}]`, data);

		}

		private initEvent(): void {
			this._socket.onSocketOpen = Handler.create(this, this.onSocketOpen, null, false);
			this._socket.onMessageReceived = Handler.create(this, this.onMessageReceived, null, false);
			//			_socket.on(Event.OPEN, this, onSocketOpen);
			//			_socket.on(Event.MESSAGE, this, onMessageReceived);
			this._socket.on(Event.CLOSE, this, this.onSocketClose, null);
			this._socket.on(Event.ERROR, this, this.onConnectError, null);
		}

		removeEvents(): void {
			this._socket.on(Event.OPEN, this, this.onSocketOpen, null);
			this._socket.on(Event.CLOSE, this, this.onSocketClose, null);
			this._socket.on(Event.MESSAGE, this, this.onMessageReceived, null);
			this._socket.on(Event.ERROR, this, this.onConnectError, null);
		}

		onSocketOpen(e: any): void {
			console.log(`[${this.getTime()}][socket]`, this._url + ' connected.');
			this._onConnected && this._onConnected.run();
			this._onConnected = null;
		}

		onSocketClose(e: any): void {
			console.log(`[${this.getTime()}][socket]`, e.target.url + ' socket closed.');
			this._socket && this._socket.close();
			// for (const handler of this._callbacks) {
			// 	handler.once && handler.recover();
			// }
			this._callbacks = this._errorHandlers = null;
			this.offAll();
		}

		onMessageReceived(buffer: ArrayBuffer): void {
			const inpkg: PackageIn = new PackageIn(buffer);
			inpkg.endian = Byte.BIG_ENDIAN;
			inpkg.parse();

			let data: any;
			if (inpkg.msgHead.type == MessageType.COMPONENT) {
				data = inpkg.content;
				console.log(`[${this.getTime()}][component message received]`, inpkg.msgHead.toString());
				this.handleComponentMsgBack(inpkg.content);
			} else if (inpkg.msgHead.type == MessageType.ENTITY) {
				if (inpkg.content === '') {
					console.log(`[${this.getTime()}][ENTITY message received]`, inpkg.msgHead.toString());
					throw new Error('entity content cannot be empty string');
				}
				data = JSON.parse(inpkg.content);
				const Seq: number = data.Seq;
				if (Seq != 0) {
					console.log(`[${this.getTime()}][received][Seq=${Seq}] ${inpkg.content}`);
					__getEnabled('json') && console.log(data.Content);
					__getEnabled('json') && console.log('')
					this.event(EventUtils.getSeqEventName('*'), data);
					const callBack: Handler = this._callbacks[Seq];
					const errorHandler: Handler = this._errorHandlers[Seq];
					delete this._callbacks[Seq];
					delete this._errorHandlers[Seq];
					data.Code > 0 && errorHandler && errorHandler.runWith(data);
					data.Code === 0 && callBack && callBack.runWith(data.Content);
					data.Code === 0 && this.event(EventUtils.getSeqEventName(data.Cmd), data.Content);
				} else {
					console.log(`[${this.getTime()}][Notify] ${inpkg.content}`);
					this.event(data.Cmd, {Content:data.Content});
					this.event('*', {Content:data.Content});
				}
			}
		}

		handleComponentMsgBack(data: any): void {
			this._componentMsgCb && this._componentMsgCb.runWith(data);
		}

		onConnectError(e: any): void {
			console.log('error', e);
		}

		private getTime(): String {
			const date: Date = new Date();
			return `${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}:${date.getMilliseconds()}`;
		}

		close(): void {
			this._socket && this._socket.close();
		}
	}
}