namespace zt.effects
{
    import BlendMode = Laya.BlendMode ;
    import Sprite = Laya.Sprite;
    import LoaderManager = Laya.LoaderManager;
    import Event = Laya.Event ;
   /**
    * 
    * 
    *
    * @since 2017.11.20
    * @author wangcongyan 
    */
    export class AniFactory
    {
        private static _ins:AniFactory ;
        private  _effectCache:object = {};
        private  _moduleCache:object = {};
        constructor()
        {

        }

        static getInstance():AniFactory
        {
            if(!this._ins)
            {
                this._ins = new AniFactory();
            }
            return this._ins;
        }

        /**
         *    
         * @param  
         * @param  
         */
        createEffect($url:string,$module:string,$endToRemove:boolean = true,$once:boolean = false,$callBack:Function = null):Effect
        {
            let moduleEffect  = this._effectCache[$module];
            if(moduleEffect == null)
            {
                moduleEffect = {};
                this._effectCache[$module] = moduleEffect;
            }
            let effectList:Effect[] =  this._effectCache[$module][$url];
            let effect:Effect ;
            if(!effectList || effectList.length == 0)
            {
                effect =  new Effect($url,$once,$endToRemove,$callBack);
                effect.index = 0 ;
                effect.module = $module ;
                effect.isSingle = false;
                effect.on('play_complete',this,this.effectComplete);
                if(!effectList)effectList = [];
                this._effectCache[$module][$url] = effectList ;
            }else
            {
                effect = effectList.pop();
                effect.index = 0 ;
                effect.callBack = $callBack ;
            }
            return effect;
        }


        /**
         *  
         */
        createSingleEffect($url:string,$module:string,$endToRemove:boolean = true,$once:boolean = false,$callBack:Function = null):Effect
        {
            let effect : Effect = this._effectCache[$url];
            if(!effect)
            {
                effect =  new Effect($url,$once,$endToRemove,$callBack);
                effect.module = $module ;
                effect.isSingle = true ;
                effect.on('play_complete',this,this.effectComplete);
                let moudleList:string[];
                if($module != '' && $module != undefined)
                {
                    moudleList = this._moduleCache[$module]
                    if(moudleList == null)
                    {
                        moudleList = [];
                        this._moduleCache[$module] = moudleList;
                    }
                    if(moudleList.indexOf($url) == -1)
                    {
                        moudleList.push($url);
                    }
                }
                this._effectCache[$url] = effect ;
            }
            return effect;
        }


        /**
         * 
         * @param 
         */
         effectComplete($target:Effect,force:boolean = false):void
        {
            const target:Effect = $target  ;
            if(target.callBack)
            {
                target.callBack(); //
            }
            if(target.isOnce) //
            {
                delete this._effectCache[$target.module];
                target.off('play_complete',this,this.effectComplete);
                target.destroy(true);
                Laya.loader.clearRes(target.url,true);
            }
            if(target.loop && target.endToRemove )
            {
                alert('');
                return ;
            }
            if(target.endToRemove || force) //
            {
                target.removeSelf();
                if(target.isSingle == false)
                {
                    this.resyleEffectGroup(target);
                }
            }
        }


        /**
         *
         */
        clearSingleEffect($url:string):void
        {
            const effect:Effect  = this._effectCache[$url] ;
            if(effect == null)return ;
            let effectList : string[];
            let index = 0 ;
            effect.removeSelf();
        }


        /**
         * 
         */
        resyleEffectGroup($effect:Effect):void
        {
            $effect.x = 0 ;
            $effect.y = 0 ;
            $effect.index = 0 ;
            $effect.isCenter = false;
            const module:string = $effect.module ;
            const url:string = $effect.url ;
            let effectObj = this._effectCache[module];
            if(!effectObj || effectObj && !effectObj[url])
            {
                this.disposeEffect($effect);  
            }else
            {
                let effectList:Effect[] = effectObj[url];
                effectList.push($effect);
                $effect.removeSelf();
            }
        }


        /**
         * 
         * @param  
         * @param  
         */
        disposeByUrl($url:string):void
        {
            const effect:Effect  = this._effectCache[$url] ;
            if(effect == null)return ;
            delete this._effectCache[$url];
            this.disposeEffect(effect);
        }


        /**
         * 
         * @param  effect
         */
        disposeEffect(effect:Effect):void
        {
            if(effect.hasListener(Event.COMPLETE))
            {
                effect.off('play_complete',this,this.effectComplete);
            }
            effect.destroy(true);
        }


        /**
         * 
         */
        disposeModule($module:string):void
        {
            let effectList = this._moduleCache[$module];
            let effect:Effect ;
            let url:string = "";
            if(effectList)
            {
                for(const url of effectList)
                    {
                        this.disposeByUrl(url);
                        Laya.loader.clearRes(url,true);
                    }
            }
            const effectObj:Object= this._effectCache[$module] ;
            if(!effectObj)return ;
            for(const key in effectObj)
            {
                const effectList = effectObj[key];
                for(const ani  of effectList)
                {
                    this.disposeEffect(ani)
                    Laya.loader.clearRes(ani.url,true);
                }
            }
            delete this._effectCache[$module] ;
        }

        /**
         * 
         * @param  $url 
         * @param  $x
         * @param  $y
         * @param  $isContray
         * @param  $length
         * @param  $parent
         */
        public createFightEffect($url: string,$x:number,$y:number,$isContray:boolean,$length:number,$parent: Sprite,$isAttack:boolean):SkillEffect
        {
            const skillEffect =  new SkillEffect($url,$x,$y,$isContray,$length,$parent,$isAttack);
            skillEffect.module = 'battle';
            return skillEffect;
        }

        /**
         *   
         */
        public  dispose():void
        {
            for(let key  in  this._moduleCache)
            {
                this.disposeModule(key);
            }
        }
    }

     /**
     * 
     * @param  url 
     * @param  module 
     * @param  endToRemove 
     * @param  once 
     * @param  callBack 
     */
    export const createEffect = ($url:string,$module:string,$endToRemove:boolean = true,$once:boolean = false,$callBack:Function = null):Effect=>
    {
        const effect:Effect = AniFactory.getInstance().createEffect($url,$module,$endToRemove,$once,$callBack);
        
        return effect;
    }
 

    /**
     * 
     * @param  url 
     * @param  module 
     * @param  endToRemove 
     * @param  once 
     * @param  callBack 
     */
    export const createSingleEffect =($url:string,$module:string,$endToRemove:boolean = true,$once:boolean = false,$callBack:Function = null):Effect=>
    {
        const effect:Effect = AniFactory.getInstance().createSingleEffect($url,$module,$endToRemove,$once,$callBack);
        return effect;
    }   


    /**
     * 
    */
    export const disposeModule = (module:string)=>
    {
        zt.effects.AniFactory.getInstance().disposeModule(module);
    }

    /**
     * 
     * @param effect 
     */
    export const resyleEffectGroup = (effect:Effect)=>
    {
        if(!!effect == false) return;
        zt.effects.AniFactory.getInstance().effectComplete(effect,true);
    }

    /**
     * 
     */
    export const playCenterQueueEffect = ($url:string,$module:string,$width:number,$height:number,$x:number = 0,$y:number=0)=>
    {
        EffectQueue.ins.addToCenterQunue($url,$module,$width,$height,$x,$y);
    }

      /**
     * 
     */
    export const playAutoQueueEffect = ($url:string,$module:string,$width:number,$height:number,$x:number = 0,$y:number = 0)=>
    {
        EffectQueue.ins.addToAutoQunue($url,$module,$width,$height,$x,$y);
    }

    /**
     * 
     * @param  url 
     * @param  module 
     * @param  endToRemove 
     * @param  once 
     * @param  callBack 
     */
    export const createSkillEffect = ($url: string,$x:number,$y:number,$isContray:boolean,$length:number,$parent: Sprite,$isAttack:boolean):SkillEffect=>
    {
        const skillEffect =  zt.effects.AniFactory.getInstance().createFightEffect($url,$x,$y,$isContray,$length,$parent,$isAttack);
        return skillEffect;
    }
}