namespace zt.app {
	import Handler = Laya.Handler;
	import Node = Laya.Node;
	import Tween = Laya.Tween;
	import EventDispatcher = Laya.EventDispatcher;

	import Layers = zt.Layers;

	/**
	 * app
	 */
	export class AppItem extends EventDispatcher {
		/**
		 * 
		 */
		private static ANIMATION_MS = 180;
		private _app: AppBase;
		private _name: string;
		private _resLoaded = false;
		private _dataLoaded = false;
		private _openX:number ;
		private _openY:number ;

		constructor(name: string, app: AppBase) {
			super();
			this._name = name;
			this._app = app;
			this._app.on(AppEvent.START_DISPOSE, this, this.onStartDispose);
		}

		start(noLoading) {
			!noLoading && showAppLoading();
			this.loadRes();
			this.loadData();
		}

		topup() { Layers.getLayer('app').addChild(this._app); }

		private loadRes(): void {
			Laya.loader.load(this._app.res, Handler.create(this, this.onResLoad), Handler.create(this, this.onResProgress, null, false));
		}

		private loadData() {
			const calls = new Calls();
			calls.addCmds(this._app.cmds);
			calls.once(Laya.Event.COMPLETE, this, () => {
				this._dataLoaded = true;
				this.checkComplete()
			});
			calls.start();
		}

		private onResLoad() {
			this.event(AppEvent.RES_LOAD);
			this._resLoaded = true;
			this._app.init();
			this._app.createSidebar(this._app.sidebarList);
			this.checkComplete();
		}

		private onResProgress(val: number) {
			updateAppLoading(val);
		}

		private checkComplete() {
			if (this._resLoaded && this._dataLoaded) {
				hideAppLoading();
				this.event(AppEvent.PRELOAD_COMPLETE)
				Laya.timer.frameOnce(2, this, this.onRenderComplete);
			}
		}

		public onStartDispose(): void {
			this._app.off(AppEvent.START_DISPOSE, this, this.onStartDispose);
			Tween.to(this._app, {
				x: this._openX ,
				y: this._openY,
				scaleX: 0,
				scaleY: 0
			}, AppItem.ANIMATION_MS, null, Handler.create(this, this.onDisposeAnimationComplete));
		}

		private onDisposeAnimationComplete(): void {
			this._app.dispose();
			this.event(AppEvent.DISPOSE);
		}

		private onRenderComplete(): void {
			this.topup();
			const w1: number = Laya.stage.width;
			const h1: number = Laya.stage.height;
			const w2: number = this._app.getAppBounds().width;
			const h2: number = this._app.getAppBounds().height;
			this._openX = Laya.stage.mouseX;
			this._openY = Laya.stage.mouseY;
			this._app.pos(this._openX,this._openY);
			this._app.scale(0, 0);
			if(this.app.adaptionType == AdaptationType.ADAPT_OPEN_POS)
			{
				this._app.xpos = this._openX ;
				this._app.ypos = this._openY ;
			}else
			{
				this._app.xpos = w1 / 2 - w2 / 2;
				this._app.ypos = h1 / 2 - h2 / 2;
			}
			this._app.start();
			this._app.adaptation();
			Tween.to(this._app, {
				x: this._app.xpos,
				y: this._app.ypos,
				scaleX: 1,
				scaleY: 1
			}, AppItem.ANIMATION_MS,null,Handler.create(this.app,this.app.resize,null));
		}

		private dispose(): void {
			Tween.to(this._app, {
				x: this._openX ,
				y: this._openY,
				scaleX: 0,
				scaleY: 0
			}, AppItem.ANIMATION_MS);
		}

		get name(): string { return this._name; }
		get level(): number { return this._app.level; }
		get app(): AppBase { return this._app; }
	}
}