namespace zt {
    export class Mask extends Laya.Sprite {
        private _free: boolean;
        constructor() {
            super();
            this.mouseThrough = true;
            this.initEvent();
        }

        private initEvent() {
            this.on(Laya.Event.MOUSE_DOWN, this, (e: Laya.Event) => {
                e.stopPropagation();
            })
        }

        draw(alpha:number) {
            this.graphics.clear();
            this.graphics.drawRect(0, 0, Laya.stage.width, Laya.stage.height, '#000000');
            this.size(Laya.stage.width, Laya.stage.height);
            this.alpha = alpha;
        }

        set free(val: boolean) {
            this._free = val;
        }

        dispose() {
            this.free = true
        }

        private static _instance: Mask;
        show(alpha:number) {
            this.draw(alpha);
            zt.Layers.getLayer('top').addChild(this);
        }

        hide() {
            this.removeSelf();
        }
        static get instance(): Mask {
            return Mask._instance || (Mask._instance = new Mask());
        }

    }

    export const DEFAULT_MASK_ALPHA = 0.4;

    export const showMask = (alpha = 0) => {
        Mask.instance.show(alpha);
    }

    export const hideMask = () => {
        Mask.instance.hide();
    }
}