namespace zt {
    import EventDispatcher = Laya.EventDispatcher;
    export const onEvents = (eventDispatcher: EventDispatcher, events: string | string[], caller: any, listener: Function) => {
        if (typeof events === 'string')
            events = [events];
        for (const event of events) {
            eventDispatcher.on(event, caller, listener);
        }
    }
}