namespace zt.app 
{
    import Handler = Laya.Handler ;
    import GComponent = fairygui.GComponent ;
    import GButton = fairygui.GButton ;
    /**
     * @usage 
     * @since 2018.01.25
     * @author wangcongyan
     */
    export class BaseSilder
    {
        protected _callBack:Handler ;
        protected _ui:GComponent ;
        protected _closeBtn:GButton ;
        protected _closeHandler:Handler ;
        constructor($ui:GComponent)
        {   
            this._ui = $ui ;
        }

        public init():void
        {
            
        }


        public addEvent():void
        {
            

        }

        public removeEvent():void
        {

        }

        public get width():number
        {
            return this._ui.getChild('bg').width;
        }

        /**
         * 
         * @param $handler   
         */
        public showSilder($handler:Handler):void
        {
            this._callBack = $handler ;
        }

        public hideSilder():void
        {
            this._closeHandler.run();
        }

        public dispose():void
        {
            throw new Error('this function must be override!~');
        }
    }
}