namespace zt
{
    import GObjectPool = fairygui.GObjectPool;
    import GObject =  fairygui.GObject ;

    /**
     * fairy 
     */
    export class Pool
    {
        private static _gobjectPool:object ;
        private static _pool:Pool;
        constructor()
        {
            if(!!Pool._gobjectPool == false)
            {
                Pool._gobjectPool = {};
            }
        }

        public static get ins():Pool
        {
            if(!!Pool._pool == false)
            {
                Pool._pool = new Pool();
            }
            return Pool._pool ;
        }

        public getComFormPool($url:string):GObject
        {
            let comPool = Pool._gobjectPool[$url];
            if(!comPool)
            {
                comPool = new GObjectPool();
                Pool._gobjectPool[$url] = comPool ;
            }
            const obj:GObject = comPool.getObject($url);
            obj.visible = true ;
            return obj;
        }

    
		public returnToPool(obj: GObject,$url:string): void {
            let comPool:GObjectPool = Pool._gobjectPool[$url];
            if(comPool)
            {
                comPool.returnObject(obj);
            }
		}

        public clear($url:string):void
        {
            let comPool = Pool._gobjectPool[$url];
            if(comPool)
            {
                comPool.clear();
                comPool = null ;
                delete  Pool._gobjectPool[$url];
            }
        }     
    }


}