namespace zt {
	export class WebParams {
		// --  -- //
		private static _isLoad = false;
		// --  init -- //
		private static _domain:string;			// cdn
		private static _userId:number;			// ID
		private static _key:string;				// key
		private static _server:string;			// 
		private static _lan = 'En';				// 

		private static init() {
			if (!WebParams._isLoad) {
				WebParams._domain = decodeURIComponent(Laya.Browser.window.init().domain);
				WebParams._key = Laya.Browser.window.init().key;
				WebParams._userId = Laya.Browser.window.init().userid;
				WebParams._server = decodeURIComponent(Laya.Browser.window.init().loginServer);
				WebParams._isLoad = true;
			}
		}
		
		static get domain():string {WebParams.init();return WebParams._domain;}

		static get key():string {
			WebParams.init();
			return WebParams._key;
		}

		static get userId():number {
			WebParams.init();
			return WebParams._userId;
		}

		static get server():string {
			WebParams.init();
			return WebParams._server;
		}

		static get lan():string {
			WebParams.init();
			return WebParams._lan;
		}
	}
}