// tslint:disable:no-magic-numbers
namespace zt {
    /**
     * (framework).
     */
    export const getTimer = (): number => {
        return Date.now() - window['zt']['__sysStartTime'];
    }

    export const getServerTime = (): number => {
        const now: number = Date.now();
        return Math.ceil(System._systemTime + (now - System._systemTimeTag) / 1000);
    }

    /**
    * 
    * @param time 
    * @param type CountDownType
    * @param dibit 
    */
    export const formatTime = (time: number, type: string = CountDownType.HMS, dibit = false, suffixs = ['h', 'm', 's']): string => {
        let countdown = getCountDown(time, type, dibit);
        countdown = countdown.map((val, index) => `${val}${suffixs[index]}`);
        return countdown.join('');
    }

    export const formatTimeHMS = (time:number): string => {
        let date: Date = new Date(time * 1000);
        let hours:number = date.getHours();
        let minutes:number = date.getMinutes();
        let seconds:number = date.getSeconds();
        let str = (hours < 10 ? "0" + hours.toString() : hours.toString());
        str += ":";
        str = str + (minutes < 10 ? "0" + minutes.toString() : minutes.toString());
        str += ":";
        str = str + (seconds < 10 ? "0" + seconds.toString() : seconds.toString());
        return str;
    }

    /**
     * 
     * @param time 
     * @param type CountDownType
     * @param dibit 
     */
    export const getCountDown = (time: number, type: string = CountDownType.HMS, dibit = false): any[] => {
        let result: any[] = [];
        if (type.indexOf('s') !== -1) {
            result.push(time % 60);
        }

        if (type.indexOf('m') !== -1) {
            result.unshift(Math.floor((time % 3600) / 60));
        }

        if (type.indexOf('h') >= 0) {
            if (type == CountDownType.HMS) {
                result.unshift(Math.floor(time / 3600));
            }
            else {
                result.unshift(Math.floor((time % 86400) / 3600));
            }
        }

        if (type.indexOf('d') >= 0) {
            result.unshift(Math.floor(time / 86400));
        }

        if (dibit)
            result = result.map((ele) => {
                return ele < 10 ? `0${ele}` : ele;
            })
        return result;
    }

    /**
     * 
     * @param hourTime '1200'
     */
    export const getSysTimeByTime = (hourTime: string): number => {
        const currDate = getDateByTime(getServerTime());
        const hour = parseInt(hourTime.substr(0, 2));
        const minute = parseInt(hourTime.substr(2, 2));
        return currDate.setHours(hour, minute, 0) / 1000;
    }

    /**
     * 
     * @param time 
     * @param offset  {'d':,'h':,'m':,'s':}
     */
    export const offsetTime = (time: number, offset: any): number => {
        const date = getDateByTime(time);
        let h = date.getHours();
        let m = date.getMinutes();
        let s = date.getSeconds();
        if (offset.d != null) {
            date.setDate(date.getDate() + offset.d);
        }
        if (offset.h != null) {
            h += offset.h;
        }
        if (offset.m != null) {
            m += offset.m;
        }
        if (offset.s != null) {
            s += offset.s;
        }
        return date.setHours(h, m, s) / 1000;
    }

    /**
     * 
     * @param time 
     */
    export const getDateByTime = (time: number): Date => {
        return new Date(time * 1000);
    }

    export class CountDownType {
        static HMS = 'hms'
        static DHMS = 'dhms'
    }
}