namespace zt.net {

	export class SocketProxy implements net.ISocketProxy {
		
		private _socket:Laya.Socket;
		private _onMessageReceived:Laya.Handler;
		private _onSocketOpen:Laya.Handler;
		constructor () {
			this._socket = new Laya.Socket();
			this._socket.on(Laya.Event.OPEN, this, this.$onSocketOpen);
			this._socket.on(Laya.Event.MESSAGE, this, this.$onMessageReceived);
		}
		
		private $onSocketOpen(e:any):void {
			this._onSocketOpen.runWith(e);
		}
		private $onMessageReceived(e:any):void {
			this._onMessageReceived.runWith(e);
		}
		
		set onMessageReceived(val:Laya.Handler) {
			this._onMessageReceived= val;
		}
		
		set onSocketOpen(val:Laya.Handler) {
			this._onSocketOpen = val;
		}
		
		connectByUrl(url:string):void {
			this._socket.connectByUrl(url);
		}
		
		on(type:string, caller:any, listener:Function, args:Array<any>=null):Laya.EventDispatcher
		{
			return this._socket.on(type, caller, listener, args);
		}
		
		close():void {
			this._socket.close();
		}
		
		get protocols():Array<any>
		{
			return this._socket.protocols;
		}
		
		send(data:any):void
		{
			this._socket.send(data);
		}
	}
}