namespace zt.universe.view {
    import Sprite = laya.display.Sprite;
    import Texture = laya.resource.Texture;
    import Handler = laya.utils.Handler;
    import Loader = laya.net.Loader;
    import Rectangle = laya.maths.Rectangle;
    import getResourceURL = zt.utils.getResourceURL;
    /**
     * @usage 
     * @author wangcongyan
     * @since 2018.01.10
     */
    export class Background extends Sprite implements ILayer{
        private _thumb: Sprite;
        private _universeData : UniverseData ;
        private _fragments:Sprite[];
        private _textureLink:string[];
        private _lastIndex:number[];
        private _addToIndex:number[];
        private _dealNums:number = 0;
        private _texCache:object = {};
        static DEALNUM:number =  1 ; //
        static SPEED = 3;
        constructor() {
            super();
            this._fragments = [] ;
            this._textureLink = [] ;
            this._lastIndex = [] ;
            this._addToIndex = [] ;
            this._thumb = new Sprite();
            this.addChild(this._thumb);
        }

        public init():void
        {

        }

        build($data : UniverseData ) : void
        {
            this._universeData = $data ;
            this.size($data.mapWidth , $data.mapHeight) ;
            this.updateThumb();
        }

        rebuild($data : UniverseData): void 
        {
            this.dispose();
            this._universeData = $data ;
            this.size($data.mapWidth, $data.mapHeight) ;
            this.updateThumb();
        }

        /**
         * 
         * @param  @$data : BaseVo
         */
        public clipView():void
        {
            if(this._universeData.isEqualLast)return ;
            const currentIndex:number[] = this._universeData.getByRect(this.x,this.y);
            for (const index of currentIndex) {
                if (this._lastIndex.indexOf(index) == -1) {
                    this._addToIndex.push(index);
                }
            }
            for (const currIndex of this._lastIndex) {
                if (currentIndex.indexOf(currIndex) == -1)
                    this.hideFragment(currIndex);
            }
            this._lastIndex = currentIndex ;
            this.drawDelay();
        }


        private drawNext(): void {
            this._dealNums = 0 ;
            Laya.timer.once(Background.SPEED, this, this.drawDelay);
        }

        private drawDelay(): void {
            if (this._addToIndex.length == 0) {
                this._dealNums = 0
                return
            };
            const index = this._addToIndex.shift();
            this.showFragment(index);
            this._dealNums ++ ;
            if(this._dealNums < Background.DEALNUM)
            {
                this.drawDelay();
            }else
            {
                this.drawNext();
            }
        }


        private showFragment($index:number):void
        {
            const baseData:BaseVo = this._universeData.getBaseByIndex($index);
            if(!baseData)return ;
            const node : Sprite =  Laya.Pool.getItemByClass('floorNode',Sprite);
            const url:string = this.getFragmentPath($index)
            node.loadImage(url);
            if(this._texCache[url] == null)
            {
                this._textureLink.push(url);
                this._texCache[url] = true ;
            }
            node.x = baseData.X ;
            node.y = baseData.Y ;
            this.addChild(node);
            this._fragments[$index] = node;
        }

        /**
         * 
         * @param  
         */
        public hideFragment($index:number):void
        {
            if(this._fragments.length > $index)
            {
                const node : Sprite = this._fragments[$index];
                this.clearNode(node);
                this._fragments[$index] = null ;
            }
        }

        private updateThumb() {
            this._thumb.graphics.clear();
            this._thumb.loadImage(this.getPath(), 0, 0, 0, 0, Handler.create(this, this.onImgLoad));
        }

        private onImgLoad(img: Texture): void {
             const scaleX: number =  this._universeData.mapWidth/ img.sourceWidth;
            const scaleY: number =  this._universeData.mapHeight/ img.sourceHeight;
            this._thumb.scale(scaleX, scaleY);
        }
        
        getPath(): string {
            return getResourceURL(`world/${this._universeData.sceneID}/thumbnail.jpg`);
        }

        getFragmentPath(index: number): string {
            return getResourceURL(`world/${this._universeData.sceneID}/fragment-${index}.jpg`);
        }

        public get name():string
		{
			return LayerType.BACKGROUND;
        }

        /**
         * 
         */
		public get ratio():number
		{
			return this._universeData.moveRatio;
        }

        /**
         * 
         */
        public clear():void
        {
            const len = this._fragments.length ;
            for(let i  = 0 ; i < len ; i ++)
            {
                this.clearNode(this._fragments[i]);
                this._fragments[i] = null ;
            }
        }

        /**
         * 
         * @param  
         */
        private clearNode($node : Sprite):void
        {
            if($node)
            {
                $node.graphics.clear();
                $node.removeSelf();
                Laya.Pool.recover('floorNode',$node);
            }
        }
        
        /**
         * 
         */
        public dispose():void
        {
            this.clear();
            this._dealNums = 0 ;
            const len  = this._textureLink.length ;
            for(let i  = 0 ; i < len ; i ++)
            {
                Laya.Loader.clearRes(this._textureLink[i]);
            }
            Laya.Pool.clearBySign('floorNode');
        }
    }
}