namespace zt.app.core{
    export class STModel
    {
        private static _instance:STModel ;
        private _modelMap:object;
        constructor()
        {
            this._modelMap = {};
        }

        public static getInstance():STModel
        {
            if(!STModel._instance)
            {
                STModel._instance = new STModel();
            }
            return STModel._instance;
        }

        public retrieveModel($modelName:string):IModel
        {
            return this._modelMap[$modelName];
        }

        public hasModel($modelName:string):boolean
        {
            return !!this._modelMap[$modelName];
        }

        public registerModel(iModel:IModel):void
        {
            const modelMap:object = this._modelMap ;
            if(modelMap[iModel.modelName])
            {
                throw new Error("");
            }
            modelMap[iModel.modelName] = iModel ;     
            iModel.init();
        }

        public removeModel($modelName:string):IModel
        {
            const modelMap:object = this._modelMap ;
            const imodel = modelMap[$modelName];
            if(imodel)
            {
                imodel.dispose();
                delete this._modelMap[$modelName];
            }
            return imodel;
        }
    }
}