/// <reference path='STController.ts' />
/// <reference path='STView.ts' />
/// <reference path='STModel.ts' />
namespace zt
{
    import STView =  zt.app.core.STView;
    import STController =  zt.app.core.STController;
    import STModel =  zt.app.core.STModel;
    export class Facade{
        private static _instance:Facade ;
        private _view:STView;
        private _model:STModel ;
        private _controller:STController ;
        private _module_hash:object ;
        constructor()
        {
            this._module_hash = {};
            this.initialize();
        }

        public static getIns():Facade
        {
            if(Facade._instance == null)
            {
                Facade._instance = new Facade();
            }
            return Facade._instance ;
        }

        protected initialize():void
        {
            this._view = STView.getInstance();
            this._model = STModel.getInstance();
            this._controller = STController.getInstance();
        }

        /**
         * 
         * @param  
         */
        public retrieveModel($modelName:string):IModel
        {
            return this._model.retrieveModel($modelName);
        }

        /**
         * 
         * @param  
         */
        public retrieveUIView($uiNameName:string):IView
        {
            return this._view.retrieveUIView($uiNameName);
        }

        /**
         * 
         * @param  
         */
        public retrieveController($controllerName:string):IController
        {
            return this._controller.retrieveController($controllerName);
        }
        

            /**
         * 
         * @param  
         */
        public registerModel($iMode:IModel):IModel
        {
             this._model.registerModel($iMode);
             this.registerMoule($iMode.modelName);
             return $iMode;
        }

        /**
         * 
         * @param  
         */
        public registerUIView($iUIView:IView):IView
        {
           this._view.registerUIView($iUIView);
           this.registerMoule($iUIView.uIName);
           return $iUIView;
        }

        /**
         * 
         * @param  
         */
        public registerController($iContorller:IController):IController
        {
            this._controller.registerController($iContorller);
            this.registerMoule($iContorller.controllerlName);
            return $iContorller;
        }

        public removeController($controllerName:string):void
        {
            let icontroller = this._controller.removeController($controllerName);
            icontroller = null;
            console.log('',$controllerName);
        }

        public removeView($uiNameName:string):void
        {
            let iview = this._view.removeUIView($uiNameName);
            iview = null;
            console.log('',$uiNameName);
        }

        public removeModel($modelName:string):void
        {
            let iModel = this._model.removeModel($modelName);
            iModel = null;
            console.log('',$modelName);
        }

        /**
         * 
         * @param  
         */
        private registerMoule($targetName:string):void
        {
           const moduleName:string = $targetName.split('_').shift() ;
           const moduleHash = this._module_hash ;
           if(!moduleHash[moduleName])
           {
                moduleHash[moduleName] = []; 
                console.log('' + moduleName);
           }
           moduleHash[moduleName].push($targetName);
           console.log('' + $targetName + ' and start()');
        }


        /**
         * 
         * @param 
         */
        public removeModule($module:string):void
        {   
            const moduleNameList = this._module_hash[$module];
            const isExist = Array.isArray(moduleNameList)  ;
            if(isExist == false)
            {
                console.log($module + '!')
                return ;
            }
            console.log($module,'');
            for(let moduleName of moduleNameList)
            {
                const module:string = moduleName;
                if(module.match('controller_xdxd'))
                {
                    this.removeController(moduleName);
                }
                if(module.match('model_xdxd'))
                {
                    this.removeModel(moduleName);
                }
                if(module.match('view_xdxd'))
                {
                    this.removeView(moduleName);
                }
            }
            this._module_hash[$module] = null ;
            delete this._module_hash[$module];
        }

         /**
         * 
         * @param 
         */
        public startModule($module:string):void
        {   
            const moduleNameList = this._module_hash[$module];
            const isExist = Array.isArray(moduleNameList)  ;
            if(isExist == false)
            {
                console.log($module + '!')
                return ;
            }
            let model:IModel;
            let controller:IController;
            let view:IView;
            for(let moduleName of moduleNameList)
            {
                const module:string = moduleName;
                if(module.match('controller_xdxd'))
                {
                    controller = this.retrieveController(moduleName);
                    controller.start();
                }
                if(module.match('model_xdxd'))
                {
                    model = this.retrieveModel(moduleName);
                    model.start();
                }
                if(module.match('view_xdxd'))
                {
                    view = this.retrieveUIView(moduleName);
                    view.start();
                    view.addEvent();
                }
                console.log('',moduleName);
            }
        }
    }

    /**
     * model
     * @param 
     * @param  
     */
    export const getModelByModuleName = ($modelName:string,$prefix:string = ''):IModel =>
    {   
        const moduleName:string = getNameByModuleAndprefix($modelName,'model',$prefix);
        return Facade.getIns().retrieveModel(moduleName);
    }

    /**
     * model
     * @param 
     * @param  
     */
    export const getControllerByModuleName = ($modelName:string,$prefix:string = ''):IController =>
    {   
        const moduleName:string = getNameByModuleAndprefix($modelName,'controller',$prefix);
        return Facade.getIns().retrieveController(moduleName);
    }


    /**
     * model
     * @param 
     * @param  
     */
    export const getViewByModuleName = ($modelName:string,$prefix:string = ''):IView =>
    {   
        const moduleName:string = getNameByModuleAndprefix($modelName,'view',$prefix);
        return Facade.getIns().retrieveUIView(moduleName);
    }   
    
    /**
     *  modelcontrollerview
     * 
     * @param moduleName 
     * @param targetName 
     * @param prefix 
     */
    export const getNameByModuleAndprefix = (moduleName:string,targetName:string,prefix:string = ''):string=>
    {
        return `${moduleName}_${prefix}_${targetName}_xdxd`;;
    }
}