namespace zt.net {
	export class PackageIn extends Laya.Byte {
		/**
		 *  uint32 
		 */
		private _msgsize: number;
		/**
		 * () uint8 
		 */
		private _flag: number;
		/**
		 *   uint32
		 */
		private _sourcesize: number;
		private _msgHead: MessageHeadIn;
		private _content: any;

		private _source: Laya.Byte;
		private _componentCMD: number;
		constructor(source: ArrayBuffer) {
			super(source);
		}

		/**
		 *  
		 */
		get msgHead(): MessageHeadIn {
			return this._msgHead;
		}

		get content(): any {
			return this._content;
		}

		parse(): void {
			this._msgsize = this.getUint32();
			this._flag = this.getUint8();
			this._sourcesize = this.getUint32();

			let msgbodyByte: Laya.Byte = this;
			if (this._flag == 1) {
				let uncompressData: Uint8Array;
				const input: Uint8Array = msgbodyByte.getUint8Array(msgbodyByte.pos, msgbodyByte.bytesAvailable);
				const opt: Object = { level: 9 };
				uncompressData = Laya.Browser.window.pako.inflate(input, opt);
				msgbodyByte = getByteObj(uncompressData.buffer);
			}

			this._msgHead = new MessageHeadIn(msgbodyByte);

			if (this._msgHead.type == MessageType.COMPONENT) {
				this._componentCMD = msgbodyByte.getUint16();
			}

			if (this._msgHead.type == MessageType.COMPONENT) {
				this._content = msgbodyByte.getUint8Array(msgbodyByte.pos, msgbodyByte.bytesAvailable).buffer;

			} else {
				msgbodyByte.getUint16();
				this._content = msgbodyByte.readUTFBytes();
			}
		}

		get componentCMD(): number {
			return this._componentCMD;
		}
	}
}