namespace zt.utils {
    /**
     * data
     * @param path 
     */
    export const getDataURL = (path: string): string => getURL('data/' + path);

    /**
     * js
     * @param name 
     */
    export const getJSURL = (name: string): string => getURL('js/' + name + '.js');

    /**
     * res
     * @param path 
     */
    export const getResourceURL = (path: string): string => getURL('res/' + path);

    /**
     * ui
     * @param path 
     */
    export const getUIURL = (path: string): string => getURL('ui/' + path);

    /**
     * .\Client\h5
     * @param path 
     */
    export const getURL = (path: string): string =>
     {
        // return zt.WebParams.domain + '/' + path;
        return "http://h5.wcy.cc:8080/" + '/' + path;
     }
}