namespace zt.tips {
    import Sprite = Laya.Sprite;
    import Tween = Laya.Tween;
    import Handler = Laya.Handler;
    import Text = Laya.Text;
    export class RaisingTipManager {
        private _last = 0;
        private _layer: Sprite;
        private _waits: string[];
        private _waitParams: any[];
        private _busy: boolean;
        private static DURATION = 1000;
        /**
         * 
         */
        private static RAISING_Y = 50;
        private static OFFSET_X: number = -10;
        private static OFFSET_Y = -5;

        add(tips: string, param?: {}) {

            if (Date.now() - this._last < RaisingTipManager.DURATION) return;
            this._last = Date.now();
            this._waits = this._waits || [];
            this._waitParams = this._waitParams || [];
            this._waits.push(tips);
            this._waitParams.push(param);
            this.checkNext();
        }

        private checkNext(): void {
            this._waits.length > 0 && !this._busy && this.next();
        }

        private next(): void {
            this._busy = true;
            const tips: string = this._waits.shift();
            const param: any = this._waitParams.shift();
            const text: Text = this.getText(tips,param);
            this._layer.addChild(text);
            const toY: number = text.y - RaisingTipManager.RAISING_Y;
            Tween.to(text, { y: toY }, RaisingTipManager.DURATION, null, Handler.create(this, this.oncomplete, [text]));
        }

        private getText(tips: string,param:any): Text {
            const text: Text = new Text();
            text.color = param.color != null ? param.color : '#E8B723';
            text.text = tips;
            // tslint:disable-next-line:no-magic-numbers
            text.fontSize = param.fontSize != null ? param.fontSize : 14;
            text.bold = param.bold != null ? param.bold : true;
            text.stroke = param.stroke != null ? param.stroke : 1;
            this.showToStage(text);
            return text;
        }

        private showToStage(tip: Text) {
            tip.x = Laya.stage.mouseX - tip.textWidth / 2;
            tip.y = Laya.stage.mouseY - tip.textHeight + RaisingTipManager.OFFSET_Y;
            if (tip.x + tip.textWidth > Laya.stage.width) {
                tip.x = Laya.stage.width - tip.textWidth - RaisingTipManager.OFFSET_X
            }
            else if(tip.x-tip.textWidth<0){
                tip.x = tip.textWidth + RaisingTipManager.OFFSET_X
            }
        }

        private oncomplete(text: Text): void {
            text.removeSelf();
            console.log(`${text.text} on completed.`)
            this._busy = false;
            this.checkNext();
        }

        private static _instance: RaisingTipManager;
        static get instance(): RaisingTipManager {
            return RaisingTipManager._instance || (RaisingTipManager._instance = new RaisingTipManager());
        }

        public init(layer: Sprite): void {
            this._layer = layer;
        }

    }
}