namespace zt.app
{
    export class AppSidebar
    {
        private _sidebarList:any[];// {sidebarName:,'type':,'params':}
        private _viewList:Object;//
        private _uiList:Object;//
        private _rendererList:Object;

        private _currName:string;//
        private _mc:Laya.Sprite;

        constructor(sidebarList:any[])
        {
            this._mc = new Laya.Sprite();
            this._sidebarList = sidebarList;
            this._currName = '';
            this._viewList = {};
            this._uiList = {};
            this._rendererList = {};
        }

        public setSidebarRenderer(name:string,renderer:Laya.Handler):void
        {
            if(this.indexOfSidebar(name) < 0)
            {
                return;
            }
            this._rendererList[name] = renderer;
        }

        public showSideBar(name:string,param:any):void
        {
            const index = this.indexOfSidebar(name);
            if(index < 0)
            {
                return;
            }
            //
            if(!this._viewList.hasOwnProperty(name))
            {
                const ui:fairygui.GObject = this._sidebarList[index][name];
                const cls:Function = this._sidebarList[index]['type'];
                let paramList:any[] = [ui];
                if(this._sidebarList[index]['params'] != null)
                {
                    paramList = paramList.concat(this._sidebarList[index]['params']);
                }
                paramList.push(param);
                cls.apply(cls,paramList);
                this._viewList[name] = cls.prototype;
                this._uiList[name] = ui;
            }
            if(this._currName != '' && this._currName != name)
            {
                const currUi:fairygui.GObject = this._uiList[this._currName];
                this._mc.removeChild(currUi.displayObject);
            }
            this._mc.addChild(this._uiList[name].displayObject);
            this._currName = name;
            if(this._rendererList[name] != null)
            {
                const renderer:Laya.Handler = this._rendererList[name];
                renderer.runWith([this._viewList[name],param]);
            }
        }

        /**
		 * 
		 * @param name 
		 */
        public getView(name:string):any
        {
            return this._viewList[name];
        }

        public get mc():Laya.Sprite
        {
            return this._mc;
        }

        public get currName():string
        {
            return this._currName;
        }

        /**
         * -1
         * @param name 
         */
        private indexOfSidebar(name:string):number
        {
            for(let i = 0;i < this._sidebarList.length;i++)
            {
                if(this._sidebarList[i].hasOwnProperty(name))
                {
                    return i;
                }
            }
            return -1;
        }

        public clear():void
        {
            this._mc.removeSelf();
            this._currName = '';
        }

        public dispose():void
        {
            if(this._viewList != null)
            {
                for(const key in this._viewList)
                {
                    this._viewList[key].dispose();
                }
            }
            this._viewList = null;
            if(this._uiList != null)
            {
                for(const key in this._uiList)
                {
                    this._uiList[key].dispose();
                }
            }
            this._uiList = null;
            if(this._mc != null)
            {
                this._mc.destroy(true);
            }
            this._mc = null;
        }

    }
}