namespace zt {
    export class FloatTool {
        private _ships: Sprite[];
        private _offsets: Laya.Dictionary;
        private static SPEED = 0.1;
        private static RANGE = 5;

        constructor() {
            this._ships = [];
            this._offsets = new Laya.Dictionary();
        }

        static add(ship: Sprite) {
            FloatTool.instance._ships.push(ship);
            FloatTool.instance._offsets.set(ship, [FloatTool.SPEED * (Math.random() > 0.5 ? 1 : -1), 0]);
        }

        static remove(ship: Sprite) {
            utils.ArrayUtil.remove(ship, FloatTool.instance._ships);
        }

        static dispose() {
            Laya.timer.clear(FloatTool.instance, FloatTool.instance.onframe);
        }

        private onframe() {
            for (const ship of FloatTool.instance._ships) {
                this.onframeItem(ship);
            }
        }

        private onframeItem(ship: Sprite) {
            const offset = this._offsets.get(ship);
            if(offset == null)
                return;
            if (offset[1] >= FloatTool.RANGE) {
                offset[0] = -FloatTool.SPEED;
            }
            else if (offset[1] <= -FloatTool.RANGE) {
                offset[0] = FloatTool.SPEED;
            }

            offset[1] += offset[0];
            ship.y += offset[0];
            this._offsets.set(ship, offset);
        }
        private static _instance: FloatTool;
        static get instance():FloatTool
        {
            if(!FloatTool._instance)
                FloatTool.init();
            return FloatTool._instance;
        }
        static set instance(f:FloatTool){ FloatTool._instance = f;}
        static init() {
            FloatTool.instance = new FloatTool();
            Laya.timer.frameLoop(1, FloatTool.instance, FloatTool.instance.onframe);
        }
    }
}