namespace zt
{
    import Sprite = Laya.Sprite ;
    import Stage = Laya.Stage;
	import Texture = Laya.Texture;
	import Handler = Laya.Handler;
    import Point = Laya.Point ;
    import Loader = Laya.Loader;
    import Rectangle = Laya.Rectangle;

    /**
     * @usage 
     * @since 2017.11.29
     * @author wangcongyan 
     */
    export class Hexagonal extends Sprite 
    {
        public edgeRange = 10;
        protected cacheGrid:object ;
        protected _mapRect:Rectangle = null;
        protected viewPorts:Array<HexData>;
        protected hasGenerate:object;
        protected _offsetX:number = 0;
        protected _offsetY:number = 0;
        protected _isLoad:boolean = false;
        protected _hexRangeList:HexRangeData[];
        constructor()
        {
            super();
            this.blendMode = Laya.BlendMode.ADD ;
            this._mapRect = new Rectangle();
        }


        /**
         *  
         */
        public init($list:HexRangeData[]):void
        {
            this.dispose();
            this.initilization();
            this._hexRangeList = $list ;
            if(this._isLoad == false)
            {
                this.loadTex()
            }else
            {
               this.clipViewPort();
            }
        }

         /**
         * 
         * @param u 
         * @param v 
         */
        protected getPointByIndex(u:number,v:number):Laya.Point
        {
            let point:Laya.Point = new Laya.Point();
            point.x = 71.75 * u + 18.75 * v;
            point.y = 11.75 * u + 41.5 * v ;
            return point ;
        }

        /**
         * 
         */
        protected loadTex():void
        {
            this.resizeMapRect();
            const url:string = zt.utils.getResourceURL('grid.json');
            Laya.loader.load(url, Handler.create(this, this.clipViewPort), null, Loader.ATLAS);
        }


        protected resizeMapRect():void
        {
            this._mapRect.x = (this._offsetX - 100);
            this._mapRect.y = (this._offsetY - 100);
            this._mapRect.width = (Laya.stage.width + 100)/this.scaleY;
            this._mapRect.height = (Laya.stage.height + 100)/this.scaleY; 
        }

        /**
         * 
         */
        protected getIndexByPoint(p:Point):Point
        {
            let point:Point = new Point();
            let u:number ;
            let v:number ;
            u = (p.x * 41.5 - p.y *18.75)/(71.75*41.50-11.75*18.75);
            v = (p.y * 71.75 - p.x *11.75)/(71.75*41.50-11.75*18.75);
            u = Math.floor(u);
            v = Math.floor(v);
            point.setTo(u,v)
            return point;
        }

        /**
         * 
         */
        public update(offsetx:number = 0,offsety:number = 0):void
        {
            const ox = -offsetx ;
            const oy = -offsety ;
            this._offsetX = ox == undefined ? 0 : ox;
            this._offsetY = oy == undefined ? 0 : oy;
            this.resizeMapRect();
            this.dispose();
            this.initilization();
            this.clipViewPort();
        }


        /**
         * 
         */
        protected showHoldRange($data:any = null):void
        {
            let center : Point ;
            let uvPoint : Point ;
            let hexData:HexData ;
            let range = 0 ;
            if(!this._hexRangeList)return ;
            for(const hexRange of this._hexRangeList)
            {
                center = new Point(hexRange['X'],hexRange['Y']);
                uvPoint =  this.getIndexByPoint(center);
                hexData =  HexData.getHex(uvPoint.x,uvPoint.y);
                range = hexRange['Range'];
                this.generateByRange(hexData,range)
                this.viewPorts.push(hexData);
                if(this.cacheGrid[hexData.u] == null)
                {
                    this.cacheGrid[hexData.u] = new Object();
                }
                this.cacheGrid[uvPoint.x][uvPoint.y] = hexData;
                this.calcHexEdge(hexData,range);
            }
        }

        /**
         * Range
         */
        protected generateByRange(hexData:HexData,range:number):void
        {
            let uvPos : Point = new Point();
            let u : number ;
            let v : number ;
            let tempHexData:HexData ;
            const maxOrigin = hexData.u + hexData.v + range ;
            const minOrigin = hexData.u + hexData.v - range ;
            for(let i = -range; i <= range; i ++)
            {
                for(let j = -range; j <= range; j ++)
                {              
                    u = hexData.u + i ;
                    v = hexData.v + j ;
                    if(i == j || u + v > maxOrigin ||u + v < minOrigin)continue ;
                    uvPos = this.getPointByIndex(u,v);
                    if(this._mapRect.contains(uvPos.x,uvPos.y))
                    {
                        if(this.cacheGrid[u] == null)
                        {
                            this.cacheGrid[u] = new Object();
                        }
                        if(this.cacheGrid[u][v] == null)
                        {
                            tempHexData =  HexData.getHex(u,v);
                            tempHexData.x = uvPos.x ;
                            tempHexData.y = uvPos.y ;
                            this.cacheGrid[u][v] =  tempHexData ;
                            this.viewPorts.push(tempHexData);
                        }
                    }
                }
            }
        }


        /**
         * 
         */
        protected clipViewPort():void
        {
            this._isLoad = true ;
            HexData.reset();
            this.showHoldRange();
            let url:string;
            let hexData:HexData ;
            const grids:Array<HexData> = this.viewPorts ;
            const len = grids.length ;
            this.graphics.clear();
            const urlConst:string  =  zt.utils.getResourceURL('grid/0.png');
            let t: Texture =  Laya.loader.getRes(urlConst);
            let gt:Texture ;
            for(let i = 0 ; i < len ; i ++ )
            {
                hexData = grids[i];
                this.graphics.drawTexture(t,hexData.x,hexData.y) ;
                if(hexData.isEdge == true )
                {
                    url = this.getEdgeSide(hexData);
                    gt =  Laya.loader.getRes(url);
                    this.graphics.drawTexture(gt,hexData.x,hexData.y) ;
                }
            }   
            // this.graphics.drawRect(this._mapRect.x,this._mapRect.y,this._mapRect.width,this._mapRect.height,0xff);
        }

        /**
         * 
         * @param hexData 
         */
        protected getEdgeSide(hexData:HexData):string
        {
            const urlNum:number = this.edgeTotalScore(hexData) ;
            const url:string =  zt.utils.getResourceURL('grid/'+urlNum+'.png') ;
            return url ;
        }


        /**
         * 
         */
        protected edgeTotalScore(hexData:HexData):number
        {
            let result:number = 0 ;
            for(let i = -1; i <= 1; i ++)
            {
                for(let j = -1; j <= 1; j ++)
                {
                    result += this.sideScore(i,j,hexData);
                }
            }
            result += hexData.needAdd ;
            return result ;
        }


        /**
         * 
         * @param u 
         * @param v 
         * @param hexData 
         */
        protected sideScore(u:number,v:number,hexData:HexData):number
        {
            let result = 0 ;
            if(this.cacheGrid[hexData.u + u] == null || 
               this.cacheGrid[hexData.u + u][hexData.v + v] == null || 
               this.cacheGrid[hexData.u + u][hexData.v + v].isEdge == false)
            {
                return  0 ;
            }
            if(u == 1 && v == -1)
            {
                return 2;
            }
            if(u == 1 && v == 0)
            {
                return 4;
            }
            if(u == 0 && v == 1)
            {
                return 8;
            }
            if(u == -1 && v == 1)
            {
                return 16;
            }
            if(u == -1 && v == 0)
            {
                return 32;
            }if(u == 0 && v == -1)
            {
                return 64;
            }
            return 0;
        }

        /**
         * 
         */
        public dispose():void
        {
            this.graphics.clear();
            this.cacheGrid = null ;
            this.viewPorts = null ;
            this.hasGenerate = null ;
        }


        protected initilization():void
        {
            this.cacheGrid = new Object();
            this.viewPorts = [];
            this.hasGenerate = new Object();
        }


        /**
         * 
         * @param hexData 
         */
        protected calcHexEdge(hexData:HexData,depth:number):void
        {   
            let  u:number = 0 ;
            let  v:number =  0 ;
            let uvPos:Point ;
           for(let i = 0 ; i < depth ; i ++)
           {
               u =  hexData.u  - depth + i  ;
               v =  hexData.v - i ;
            
               this.edge(u,v,1);

               u =  hexData.u  - depth  ;
               v =  hexData.v + i ;
            
              this.edge(u,v,1);

               u =  hexData.u  - depth + i;
               v =  hexData.v + depth ;
            
               this.edge(u,v,1);

               u =  hexData.u  + i;
               v =  hexData.v + depth - i;
            
               this.edge(u,v,i == 0 ? 1:0);

               u =  hexData.u  + depth;
               v =  hexData.v - i;
            
               this.edge(u,v,i == 0 ? 1:0);

               u =  hexData.u  + depth -i;
               v =  hexData.v - depth;
               this.edge(u,v,i == 0 ? 1:0);

               u =  hexData.u  - i;
               v =  hexData.v - depth + i ;
               this.edge(u,v,1);
           }
        }

        /**
         * 
         * @param u
         * @param v 
         */
        protected edge(u:number,v:number,add:number = 0 ):void
        {
            if(this.cacheGrid[u] == null)
            {
                this.cacheGrid[u] = new Object();
            }
            const uvPos = this.getPointByIndex(u,v);
            if(this._mapRect.contains(uvPos.x,uvPos.y))
            {
                if(this.cacheGrid[u][v] == null)
                {
                    const hexData =  HexData.getHex(u,v);
                    hexData.x = uvPos.x ;
                    hexData.y = uvPos.y ;
                    this.cacheGrid[u][v] = hexData ;
                    this.viewPorts.push(hexData);
                }
                if(this.cacheGrid[u][v].isEdge == true)return;
                this.cacheGrid[u][v].isEdge = true ;
                this.cacheGrid[u][v].needAdd = add ;
            }
        }
    }

    /**
     * 
     */
    export class HexData
    {
        public u:number  ;
        public v:number  ;
        public x:number  ;
        public y:number  ;
        public isEdge:boolean ;
        public inEdge:boolean ; //
        public needAdd:number = 0 ; // 1
        static hexDatas:Array<HexData> = [];
        static index:number = 0 ;
        constructor(ou:number,ov:number)
        {
            this.u = ou ;
            this.v = ov ;
        }
        get key(){return this.u.toString() + this.v.toString()}

        /**
         * 
         * @param index 
         * @param u 
         * @param v 
         */
        static getHex(u:number,v:number):HexData
        {
            let hexData:HexData = HexData.hexDatas[HexData.index];
            if(hexData == null)
            {
                hexData = new HexData(u,v);
                HexData.hexDatas[HexData.index] = hexData ;
            }else
            {
                hexData.u = u ;
                hexData.v = v ;
            }
            hexData.isEdge = false;
            HexData.index ++ ;
            return hexData ;
        }

        static reset():void
        {
            HexData.index = 0 ;
        }

        static clear():void
        {
            HexData.hexDatas = null ;  

        }

    }

    /**
     * 
     */
    export interface HexRangeData
    {
        X:number,
        Y:number,
        Range:number
    }

}