/// <reference path='TipManager.ts' /> 
/// <reference path='RaisingTipManager.ts' /> 
/// <reference path='RaisingItemManager.ts' /> 
/// <reference path='RaisingImageManager.ts' /> 
/// <reference path='TipCaches.ts' />
/// <reference path='items/BaseTip.ts' />
/// <reference path='items/CommonTip.ts' />
/// <reference path='items/NormalTip.ts' />
/// <reference path='items/SimpleTip.ts' />
import Sprite = Laya.Sprite;
import TipManager = zt.tips.TipManager;
import RaisingTipManager = zt.tips.RaisingTipManager;
import RaisingImageManager = zt.tips.RaisingImageManager;
import RaisingItemManager = zt.tips.RaisingItemManager ;
import TipCaches = zt.tips.TipCaches;

import CommonTip = zt.tips.CommonTip;
import NormalTip = zt.tips.NormalTip;
import SimpleTip = zt.tips.SimpleTip;
import RaiseParams = zt.RaiseParams;

namespace zt {
	/**
	 * 
	 */
	export class Tips {
		/**
		 * tip 
		 * @param tip stringJSON Object
		 */
		static show(tip: string | UIBase | string[]): void {
			if (typeof tip === 'string') {
				Tips.showSimpleTip(tip);
			} else if (Array.isArray(tip)) {
				Tips.showTipNormal(tip);
			}
			else {
				Tips.showCommonTip(tip);
			}
		}

		private static isJSON(str: string): boolean {
			if (str.indexOf('{') === -1 && str.indexOf('[') === -1) return false;
			try {
				JSON.parse(str);
				return true;
			}
			catch (e) {
				return false;
			}
		}

		/**
		 * tips
		 * @param tips 
		 * @param type  012
		 * @param param  {
		 * 							'color':string 
		 * 							'fontSize':number 
		 * 							'bold':boolean 
		 * 							'stroke':number 
		 * 						}
		 */
		static showRaisingTips(tips: string, type = 0, param?: {}): void {
			switch (type) {
				case 0:
					param = {};
					break;
				case 1:
					param = { 'color': '#5cff92' };
					break;
				case 2:
					param = { 'color': '#e03645' };
					break;
				case 3:
					param = param || {};
					break;
			}
			RaisingTipManager.instance.add(tips, param);
		}


		static showRaiseItem(data:any): void 
		{	
			RaisingItemManager.instance.add(data);
		}

		static registerRaiseItem(key:string,url:string): void 
		{	
			RaisingItemManager.instance.registerRaiseItem(key,url);
		}

		static playRaiseItem(): void 
		{	
			RaisingItemManager.instance.play();
		}
		
		/**
		 * tips
		 * @param url 
		 * @param param  
		 */
		static showRaisingImages(url: string,param:RaiseParams): void 
		{	
			RaisingImageManager.instance.add(url, param);
		}

		private static showTipNormal(tip: string[]): void {
			Tips.showTipByType(TipType.NORMAL, tip);
		}

		private static showSimpleTip(tip: string): void {
			Tips.showTipByType(TipType.SIMPLE, tip);
		}

		private static showCommonTip(ui: UIBase): void {
			Tips.showTipByType(TipType.COMMON, ui);
		}

		static hide(): void {
			TipCaches.getInstance().clearCache();
			TipManager.instance.hide();
		}

		static showTipByType(type: String, data: UIBase | string | string[]): void {
			TipManager.instance.show(type, data);
		}

		static register(type: string, cls: any): void {
			TipCaches.register(type, cls);
		}

		static init(): void {
			TipManager.instance.init(Tips.tipLayer);
			RaisingTipManager.instance.init(Tips.tipLayer);
			RaisingImageManager.instance.init(Tips.tipLayer);
			RaisingItemManager.instance.init(Tips.tipLayer);
			Tips.register(TipType.SIMPLE, SimpleTip)
			Tips.register(TipType.NORMAL, NormalTip)
			Tips.register(TipType.COMMON, CommonTip)
		}

		private static get tipLayer(): Sprite {
			return zt.Layers.getLayer('tip') as Sprite;
		}
	}

	/**
	 * 
	 * @param $start   
	 * @param $end 
	 * @param $duration 
	 * @param $isQuene 
	 */
	export const getRaiseParam =($start:IPoint,$end:IPoint,$isQuene:boolean = false,$duration:number = 800,$delta:number = 30):RaiseParams=>
	{
		const param = {"start":$start,"end":$end,"duration":$duration,'isQuene':$isQuene,detalTime:$delta};
		return param ;
	}
}

class TipType {
	static SIMPLE = 'simple';
	static NORMAL = 'normal';
	static COMMON = 'comomn';
}
