namespace zt
{
    import Sprite = Laya.Sprite ;
    import Stage = Laya.Stage;
	import Texture = Laya.Texture;
	import Handler = Laya.Handler;
    import Point = Laya.Point ;
    import Loader = Laya.Loader;
    import Rectangle = Laya.Rectangle;
    /**
     * @usage   
     * @author wangcongyan
     * @since 2017.11.27
     */
    export class GuideLine extends Sprite
    {
        /***  */
        public  pathLength:number = 87;
        protected _mapRect:Rectangle = null;
        protected _offsetX:number = 0;
        protected _offsetY:number = 0;
        protected _url:string ;
        protected _start:Point ;
        protected _end:Point ;
        protected _pathList:PathData[];
        protected _type:string;
        protected _isLoad:boolean = false;  
        protected _angle:number;
        private _frame:number = 0;
        private lightUrl0:string ;
        private lightUrl1:string ;
        private lightUrl2:string ;
        private _data:GuideLineData;
        constructor()
        {
            super();
            this.lightUrl0 =   zt.utils.getResourceURL('universe/line/0.png');
            this.lightUrl1 =   zt.utils.getResourceURL('universe/line/1.png');
            this.lightUrl2 =   zt.utils.getResourceURL('universe/line/2.png');
            this.blendMode = Laya.BlendMode.ADD ;
        }

        /**
         * 
         */
        public init():void
        {
            Laya.timer.clear(this,this.reRender);
            Laya.timer.loop(200,this,this.reRender);
        }

         /**
         * 
         */
        public clear():void
        {
            Laya.timer.clear(this,this.reRender);
        }

        get data():GuideLineData
        {
            return this._data ;
        }

        set data(value:GuideLineData)
        {
            this._data = value ;
        }

        /**
         *
         * */
        private reRender():void
        {
            this._frame ++ ;
            this.graphics.clear();
            let x : number,y : number;
            const frame = this._frame % 3 ;
            const urlConst : string  =   this['lightUrl' + frame]   ;
            const t : Texture =  Laya.loader.getRes(urlConst);
            const w = t.width ;
            const h = t.height ;
            const matrix:Laya.Matrix = new Laya.Matrix();   
            matrix.rotate(this._angle);    
            for(const pathData of this._pathList)
            {
                x =  pathData.x ;
                y =  pathData.y;
                this.graphics.drawTexture(t,x,y);
            }
            // this.tempAngle += 10 ;
            // this.rotation = this.tempAngle ;
        }

          /**
         * 
         */
        protected loadTex():void
        {
            this._mapRect = new Rectangle();
            this._mapRect.x = this._offsetX - 100;
            this._mapRect.y = this._offsetY - 100;
            this._mapRect.width = Laya.stage.width + 100;
            this._mapRect.height = Laya.stage.height + 100; 
            const url:string = this.url;
            Laya.loader.load(url, Handler.create(this, this.clipViewPort), null, Loader.ATLAS);
        }


        /**
         * 
         */
        protected get url():string
        {
            if(this._url == undefined || this._url == null)
            {
                this._url = zt.utils.getResourceURL('universe/line.json');
            }
            return this._url;
        }

        /**
         *
         */
        protected clipViewPort():void
        {
            this._isLoad = true ;
            this._pathList = [];
            const distance = this.getDistance(this._start,this._end);   
            const len:number = Math.ceil( distance / this.pathLength);
            const pathTotal:Point = new Point();
            let pathData:PathData ;
            let t:Texture ;
            // PathData.reset();
            for(let i:number = 0 ; i < len ; i ++)
            {
                pathTotal.x = this.pathLength * i ;
                pathData =  new PathData(pathTotal.x,0);
                // PathData.getPath(pathTotal.x,pathTotal.y);
                this._pathList.push(pathData);
            }	
            if(this._pathList.length > 0)
            {
                this.init();
            }else
            {
                this.clear();
            }
            this.pivotX = 0.5 ;
            this.pivotY = 0.5 ;
            this.x = this._start.x;
            this.y = this._start.y;
            this.rotation = this._angle ;
        }


        /**
         * 
         * @param  
         * @param  
         */
        protected getDistance($start:Point,$end:Point):number
        {
            let distance:number = 0;
            distance = $start.distance($end.x,$end.y);
            const lx = $end.x - $start.x;
            const ly = $end.y - $start.y;
            this._angle  = Math.atan2(ly, lx) * 180 / Math.PI ;
            return  distance;
        }

        

        /**
         * 
         */
        public updatePath($start:Point,$end:Point):void
        {   
            this._start = $start ;
            this._end = $end ;
            if(this._isLoad)
            {
                this.clipViewPort();
            }else
            {
                this.loadTex();
            }
        }   

        /**
         *  
         */
        public update(offsetx:number = 0,offsety:number = 0):void
        {
            this._offsetX = -offsetx ;
            this._offsetY = -offsety ;
            this._mapRect.x = this._offsetX - 100;
            this._mapRect.y = this._offsetY - 100;
            this._mapRect.width = Laya.stage.width + 100;
            this._mapRect.height = Laya.stage.height + 100;
            this.clipViewPort();
        }

        /**
         * 
         */
        public dispose():void
        {
            this.clear();
            PathData.clear();
            this.graphics.clear();
        }
    }

    /**
     * 
     */
    export class GuideLineData
    {
        /**
        * 
        */
        public guideID:number ;
        public to:number ;
        public from:number;
        public minLevel:number ;
        public maxLevel:number ;

    }

     /**
     * 
     */
    export class PathData
    {
        private _x:number  ;
        private _y:number  ;
        static pathDatas:Array<PathData> = [];
        static index:number = 0 ;
        constructor(x:number,y:number)
        {
            this._x = x ;
            this._y = y ;
        }

        /**
         * 
         * @param index 
         * @param u 
         * @param v 
         */
        static getPath(x:number,y:number):PathData
        {
            let pathData:PathData = PathData.pathDatas[HexData.index];
            if(pathData == null)
            {
                pathData = new PathData(x,y);
                PathData.pathDatas[HexData.index] = pathData ;
            }else
            {
                pathData.x = x ;
                pathData.x = y ;
            }
            PathData.index ++ ;
            return pathData ;
        }

        set x(value:number)
        {
            this._x = value ;
        }

        get x():number
        {
            return this._x ;
        }

        set y(value:number)
        {
            this._y = value ;
        }

        get y():number
        {
            return this._y ;
        }

        static reset():void
        {
            PathData.index = 0 ;
        }

        static clear():void
        {
            PathData.pathDatas = null ;  
        }
    }
}