namespace zt.net {

	export class PackageOut extends Laya.Byte {
		/**
		 * 
		 */
		private static COMPRESS_BYTES_MIN = 255;
		/**
		 * body
		 */
		private static BODY_BYTES = 4;
		private _componentCMD:number;

		private _flag:number;

		private _msgbodyBuffer:ArrayBuffer;

		private _msgbody:Laya.Byte;
		
		constructor() {
			super();
			this.endian = Laya.Byte.BIG_ENDIAN;
		}
		
		writeData(messageHead:MessageHeadOut, componentCMD = 0, msg=''):void {
			this._componentCMD = componentCMD;
			
			const msgSourceByte:Laya.Byte = getByteObj();

			let msgUTF:Laya.Byte = new Laya.Byte();
			msgUTF.writeUTFBytes(msg);
			msgSourceByte.writeUint16(msgUTF.length);
			msgSourceByte.writeUTFBytes(msg);
			// msgSourceByte.writeUTFBytes('\0');
			
			this._msgbody = getByteObj();
			messageHead.write(this._msgbody);
			if(messageHead.type== MessageType.COMPONENT)
				this._msgbody.writeUint16(this._componentCMD);
			this._msgbody.writeArrayBuffer(msgSourceByte.buffer);
			
			this._msgbodyBuffer = this._msgbody.buffer;
			
			this._flag = this.getFlag(this._msgbody);
			if(this._flag == FlagType.COMPRESS) {
				this._msgbodyBuffer = this.compress(this._msgbody);
			}
			
			this.writeUint32(this.getmsgsize(this._msgbodyBuffer));
			this.writeUint8(this._flag);
			this.writeUint32(this._msgbody.length);
			this.writeArrayBuffer(this._msgbodyBuffer);
		}
		
		getmsgsize(msgbodyBuffer:ArrayBuffer):number {
			return 1 + PackageOut.BODY_BYTES + msgbodyBuffer.byteLength;
		}
		
		compress(msgSourceByte:Laya.Byte):ArrayBuffer
		{
			let compressData:ArrayBuffer;
			const opt:Object = {level:9};
			compressData = Laya.Browser.window.pako.deflate(new Uint8Array(msgSourceByte.buffer), opt).buffer;
			return compressData;
		}
		
		getFlag(msgbody:Laya.Byte):number
		{
			let flag:number = FlagType.COMPRESS;
			msgbody.length <= PackageOut.COMPRESS_BYTES_MIN && (flag = FlagType.UNCOMPRESS);
			return flag;
		}

		get flag():number {
			return this._flag;
		}

		get msgBodyLen():number {
			return this._msgbodyBuffer.byteLength
		}

		get msgbody():Laya.Byte
		{
			return this._msgbody;
		}
	}
}