namespace zt.app.core{
    export class STController
    {
        private static _instance:STController ;
        private _controllerMap:object ;
        constructor()
        {
            this._controllerMap = {};
        }

        public static getInstance():STController
        {
            if(!STController._instance)
            {
                STController._instance = new STController();
            }
            return STController._instance;
        }


        public retrieveController($controllerName:string):IController
        {
            return this._controllerMap[$controllerName];
        }

        public hasController($controllerName:string):boolean
        {
            return !!this._controllerMap[$controllerName];
        }

        public registerController($icontroller:IController):void
        {
            const controllerMap:object = this._controllerMap ;
            if(controllerMap[$icontroller.controllerlName])
            {
                throw new Error("");
            }
            controllerMap[$icontroller.controllerlName] = $icontroller ;     
            $icontroller.init();
        }

        public removeController($controllerName:string):IModel
        {
            const controllerMap:object = this._controllerMap ;
            const icontroller = controllerMap[$controllerName];
            if(icontroller)
            {
                icontroller.dispose();
                delete this._controllerMap[$controllerName];
            }
            return icontroller;
        }
    }

}   