namespace zt.app
{
    import GComponent = fairygui.GComponent;
    import Event = Laya.Event;
    /**
     * @usage 
     * @since 2017.12.28
     * @author wangcongyan
     */
    export class UIView implements IView
    {   
        private _uiViewName:string = 'UIView';
        private _model:IModel ;
        private _controller:IController ;
        protected _moduleName:string;
        protected _ui:GComponent ;
        private _prefix:string;
        constructor($root?:GComponent)
        {
            this._ui = $root ;
        }

        public getUI($d:object,$isGuide:boolean):GuideNode
        {
            throw new Error('this function must be override!');
        }

        public startGuild($d:object):void
        {

        }

        public start():void
        {
            throw new Error('this function must be override!');
        }

        public setModuleName(moduleName:string,$prefix:string):void
        {
            this._moduleName = moduleName ;
            this._prefix = $prefix ? $prefix : '';
            this._uiViewName = getNameByModuleAndprefix(this._moduleName,'view',this._prefix);
        }

        public init():void
        {
            throw new Error('this function must be override!');
        }
        
        public addEvent():void
        {
            throw new Error('this function must be override!');
        }   

        protected removeEvent():void
        {
            throw new Error('this function must be override!');
        }

        protected closeApp(event:Event = null):void
        {
            this.controller.close();
        }

        public get model():IModel
        {
            if(!this._model)
            {
                this._model =  getModelByModuleName(this._moduleName);
            }
            return this._model ;
        }

        public get controller():IController
        {
            if(this._controller == null)
            {
                this._controller = getControllerByModuleName(this._moduleName);
            }
            return this._controller ;
        }

        public get prefix():string
        {
            return this._prefix;
        }
        
        public get moduleName():string
        {
            return this._moduleName ;
        }

        public get uIName():string
        {
            return this._uiViewName;
        }

        public get ui():GComponent
        {
            return this._ui ;
        }

        public dispose():void
        {
            this.removeEvent();
            throw new Error('this function must be override!');
        }

    }

}