namespace zt.effects
{   
    /** 
     *@description 
     *@author wangcongyan
     *@since 2018.02.27
    */
    export class EffectQueue
    {   
        private static _ins:EffectQueue ;
        private _effectQueue:object ;
        static CENTER = 'center';
        static AUTO = 'auto';
        private _isCenterPlaying:boolean = false ;
        private _isAutoPlaying:boolean = false ;
        constructor()
        {
            this._effectQueue = {};
        }

        /**
         * 
         */
        public static get ins():EffectQueue
        {
            if(EffectQueue._ins == null)
            {
                EffectQueue._ins = new EffectQueue();
            }
            return EffectQueue._ins;
        }

        /** 
         * @description 
        */
        public addToCenterQunue($url:string,$module:string,$width:number,$height:number,$x:number,$y:number):void
        {
            const effectData = this.getEffectData($url,$module,$width,$height,$x,$y,EffectQueue.CENTER);
            if(!!this._effectQueue[EffectQueue.CENTER] == false)
            {
                this._effectQueue[EffectQueue.CENTER] = [];
            }
            this._effectQueue[EffectQueue.CENTER].push(effectData);
            this.playEffect(EffectQueue.CENTER);
        }

          /** 
         * @description 
        */
       public addToAutoQunue($url:string,$module:string,$width:number,$height:number,$x:number,$y:number):void
       {
           const effectData = this.getEffectData($url,$module,$width,$height,$x,$y,EffectQueue.CENTER);
           if(!!this._effectQueue[EffectQueue.CENTER] == false)
           {
               this._effectQueue[EffectQueue.CENTER] = [];
           }
           this._effectQueue[EffectQueue.CENTER].push(effectData);
           this.playEffect(EffectQueue.CENTER);
       }

        private playEffect($type:string):void
        {
            const effectList:any[] = this._effectQueue[$type];
            if(this._isCenterPlaying)return ;
            if(!effectList ||effectList.length == 0)
            {
                this._isCenterPlaying = false;
                return ;
            } 
            let effectData = effectList.shift();
            const effect:effects.Effect = zt.effects.createEffect(effectData.url,effectData.module,true);
            const container:Sprite = zt.Layers.getLayer('top') as Sprite ;
            const w:number = Laya.stage.width ;
            const h:number = Laya.stage.height ;
            let offsetX =  effectData.x;
            let offsetY =  effectData.y;
            if($type == EffectQueue.CENTER)
            {
                offsetX = (w - effectData.width) /2 ;
                offsetY = (h - effectData.height) /2 ;
            }
     
            effect.playEffect(container,offsetX,offsetY,false,Laya.BlendMode.NORMAL,null,effectData.speed);
            this._isCenterPlaying = true ;
            effect.on('play_complete',this,this.playEffectComplete,[EffectQueue.CENTER]);
            effectData = null;
        }

        

        /**
         * 
         * @param  $type
         */
        private playEffectComplete($type:string):void
        {
            this._isCenterPlaying = false;
            this.playEffect($type);
        }

        /**
         * 
         * @param  $url
         * @param  $module
         * @param  $width
         * @param  $height
         * @param  $type
         */
        private getEffectData($url:string,$module:string,$width:number,$height:number,$x:number,$y:number,$type:string):EffectData
        {
            const effectData:EffectData = new EffectData();
            effectData.url = $url ;
            effectData.module = $module ;
            effectData.width = $width ;
            effectData.height = $height;
            effectData.x = $x ;
            effectData.y = $y ;
            effectData.type = $type;
            return effectData;
        }
    }

    export class EffectData
    {
        public url:string ;
        public module:string;
        public width:number;
        public height:number;
        public type:string ;
        public speed:number = 60;
        public x:number;
        public y:number;
    }
}