namespace zt.universe
{
    /**
	 *@usage
	 *@author wangcongyan
	 *@since 2018.01.12
     */
    export class GridVo
    {   
        private  _rIndex:number=0;
		private  _cIndex:number=0;
		private  _w:number=0;
		private  _h:number=0;
		private  _x:number=0;
		private  _y:number=0;
		private  _url:string="";
        private  _key:string="";
		private _elementKeyList:any[];
		private _isOnShow:boolean = false;

		constructor()
		{
			this._elementKeyList=[];
		}
		
		public set rIndex(val:number){this._rIndex=val;this.updateKey();}
		public set cIndex(val:number){this._cIndex=val;this.updateKey();}
		public set width(val:number){this._w=val;}
		public set height(val:number){this._h=val;}
		public set x(val:number){this._x=val;}
		public set y(val:number){this._y=val;}
		
		public get key():string{return this._key;}
		public get rIndex():number{return this._rIndex;}
		public get cIndex():number{return this._cIndex;}
		public get width():number{return this._w;}
		public get height():number{return this._h;}
		public get x():number{return  this._cIndex*this.width;}
		public get y():number{return this._rIndex*this.height;}
		
		private  updateKey():void
		{
			this._key= this._rIndex+","+this._cIndex;
		}
		
		public  registerKey(iGrid:IElementVo):void
		{
			this._elementKeyList.push(iGrid);
		}
		
		public  deleteKey(iGrid:IElementVo):void
		{
			if(this._elementKeyList.indexOf(iGrid)!=-1)
			{
				this._elementKeyList.splice(this._elementKeyList.indexOf(iGrid),1);
			}
		}
		
		public get elementKeyList():any[]
		{
			return this._elementKeyList.concat();
		}

		public get isOnShow():boolean
		{
			return this._isOnShow ;
		}

		public set isOnShow(value:boolean)
		{
			this._isOnShow = value ;
		}

		public clear():void
		{
			this._elementKeyList.length = 0 ;
			this._elementKeyList = null;
		}	
    }
}