namespace zt {
	import Node = Laya.Node;
	import Sprite = Laya.Sprite ;
	export class Layers {
		private static _layers: Object;

		public static init(layers: Array<any>): void {
			const root = Layers.createLayer(Laya.stage,0);
			root.name = 'root';
			Laya.stage.addChild(root);
			Layers._layers = {};
			let layerName: string, layer: Laya.Node;
			for (let i = 0; i < layers.length; i++) {
				layerName = layers[i];
				layer = Layers.createLayer(root,i);
				layer.name = 'layer:' + layerName;
				this._layers[layerName] = layer;
				root.layerList.push(layer as LayerNode);
				root.addChild(layer);
			}
		}

		private static createLayer($root:Sprite,$index:number): LayerNode {
			return new LayerNode($root,$index);
		}

		public static getLayer(layerName: string): Laya.Node {
			return Layers._layers[layerName];
		}

	}

	/**
	 * 
	 */
	export class LayerNode extends Sprite
	{
		private _root:LayerNode|Sprite ;
		private _index:number;
		private _layerList:LayerNode[]= [] ;
		constructor($root:LayerNode|Sprite,index:number)
		{
			super();
			this._root = $root ;
		}

		public show():void
		{
			(this._root as LayerNode).repainted();
		}	

		public get index():number
		{
			return this._index;
		}

		public hide():void
		{
			this._root.removeChild(this);
		}

		/**
		 * 
		 */
		public repainted():void
		{
			let layer : LayerNode ;
			const layerListCopy:LayerNode[] = this._layerList.sort(this.sortFun);
			for(let i = 0 ; i < this.numChildren ; i ++)
			{
				layer = layerListCopy[i];
				this.addChild(layer);
			}
		}

		private sortFun(a:LayerNode,b:LayerNode):number
		{
			if(a.index > b.index)
			{
				return 1 ;
			}	
			if(a.index < b.index)
			{
				return -1 ;
			}
		}

		public get layerList():LayerNode[]
		{
			return this._layerList;
		}
	}

}