namespace zt.data {
	export class BaseDataHash {

		// -- CONSTANT -- //
		protected static KEY_SPACE = '_';

		protected _values: Array<any> = [];			// 
		protected _keys: Array<any> = [];			// 
		protected _dic: object = {};				// 
		protected _name: string;					// 
		protected _itemCls: any;					// 
		protected _mainKeys: Array<string>;			// key

		/**
		 * @param name 
		 * @param data json,json
		 * @param itemCls 
		 */
		constructor(name: string, data: any = null, mainKeys: any = null, itemCls: any = null) {
			this._name = name;
			this._itemCls = itemCls ? itemCls : this.defaultItemCls;
			this._mainKeys = mainKeys instanceof Array ? mainKeys : [mainKeys];
			let list: Array<any>;
			// -- list begin -- //
			data = data ? data : [];
			list = typeof data == 'string' ? JSON.parse(data) : data;
			// --  -- //
			for (const listItem of list) {
				const item: any = new this._itemCls(listItem);
				this._values.indexOf(item) == -1 && this._values.push(item);
				const key: any = this.getItemKey(this._mainKeys, item);
				this._keys.indexOf(key) == -1 && this._keys.push(key);
				this._dic[key] = item;
			}
			list = null;
		}

		/**
		 * length
		 */
		public getlength(): number {
			return this._keys.length;
		}

		/**
		 * 
		 */
		public getList() {
			return this._dic;
		}

		/**
		 *  
		 * @param keyId 
		 */
		public getItemByMainKey(keyId: any): object {
			if (keyId instanceof Array) {
				keyId = keyId.join(BaseDataHash.KEY_SPACE);
			}
			return this._dic[keyId];
		}		

		/**
		 * keykey ,hash
		 * @param keyName key,["ItemID","Level"],"ItemID"
		 * @param keyValue idkeyName[12,2],12
		 */
		public searhItems(keyName: any, keyValue: any): Array<any> {
			return keyName instanceof Array ?
				this.searchItemsByKeys(keyName, keyValue) :
				this.searchItemsByKey(keyName, keyValue);
		}

		private searchItemsByKey(key: string, keyId: any): Array<any> {
			if ([key] == this._mainKeys) return [this.getItemByMainKey(keyId)];
			const result: Array<any> = [];
			for (const valueItem of this._values) {
				const itemKey: any = valueItem[key];
				if (itemKey == keyId) {
					result.push(valueItem);
				}
			}
			return result;
		}

		private searchItemsByKeys(keys: Array<any>, keyIds: Array<any>): Array<any> {
			const key: string = keys.join(BaseDataHash.KEY_SPACE);
			const keyValue: String = keyIds.join(BaseDataHash.KEY_SPACE);
			if (this._mainKeys instanceof Array && key == this._mainKeys.join(BaseDataHash.KEY_SPACE)) return [this.getItemByMainKey(keyValue)];
			const result: Array<any> = [];
			for (const valueItem of this._values) {
				const itemKey: string = this.getItemKey(keys, valueItem);
				if (itemKey == keyValue) {
					result.push(valueItem);
				}
			}
			return result;
		}

		protected checkMainKey(item: object) {
			const mainKey: Array<string> = this._mainKeys instanceof Array ? this._mainKeys : [this._mainKeys];
			for (const key of mainKey) {
				const tmp: any = item[key];
				if (!tmp) return null;
			}
			return true;
		}

		protected getItemKey(keys: Array<string>, data: object): string {
			return keys.length == 1 ?
				data[keys[0]] :
				keys.reduce((p, n) => {
					const t = p[0].toLowerCase() + p.substring(1, p.length);
					n = n[0].toLowerCase() + n.substring(1, n.length);
					if (data.hasOwnProperty(t)) {
						return data[t] + BaseDataHash.KEY_SPACE + data[n];
					} else {
						return p + BaseDataHash.KEY_SPACE + data[n];
					}
				});
		}

		// 
		protected get defaultItemCls(): any { return BaseDataItem; }
		public get length(): number { return this._values.length; }
		public get name(): string { return this._name; }
		public get values(): Array<any> { return this._values.concat(); }
		public get keys(): Array<any> { return this._keys.concat(); }
	}
}