namespace zt.data
{
	/**
	 *  
	 */	
	export class DataHash extends BaseDataHash {
		
		public static D_UP='D_UP_';
		public static D_DEL = 'D_DEL_';
		public static D_ADD = 'D_ADD_';

		/**
		 * @param name 
		 * @param data json
		 * @param mainKey 
		 * @param itemCls 
		 */		
		constructor (name: string, data: any = '', mainKey: any = 'ID', itemCls: any = null) {
			super(name, data, mainKey, itemCls);
		}
		
		get defaultItemCls():any { return DataItem; }
		
		/**
		 *  
		 * @param item 
		 */		
		public removeItem(item:object):object {
			if (!this.checkMainKey(item)) return null;
			const key:string = this.getItemKey(this._mainKeys, item);
			const preItem:object=this.getItemByMainKey(key);
			delete this._dic[key];
			let index:number=this._keys.indexOf(key);
			index!=-1 && this._keys.splice(index,1);
			index=this._values.indexOf(preItem);
			index!=-1 && this._values.splice(index,1);
			zt.ctrl.GameCtrl.intance.event(DataHash.D_DEL+this.name, preItem);
			return item;
		}


		/**
		 * 
		 */
		public clearAll():void
		{
			for(let key of this._keys)
			{
				this._dic[key] = null ;
				delete this._dic[key];
			}
			this._keys = [];
			this._values = [];
		}
		
		/**
		 *  
		 * @param item 
		 */		
		public updateItem(item: object): object {
			if (!this.checkMainKey(item)) return null;
			const key:string = this.getItemKey(this._mainKeys, item);
			let preItem:any=this.getItemByMainKey(key);
			if(preItem==null) {
				preItem=this.addItem(item);
			} else {
				if(preItem.hasOwnProperty('hasUpdate')) {
					preItem.update(item);
				} else {
					const index:number=this._values.indexOf(item);
					index==-1 ? this._values.push(item) : this._values[index]=item;
					this._dic[key]=item;
					preItem=item;
				}
				zt.ctrl.GameCtrl.intance.event(DataHash.D_UP+this.name, preItem);
			}
			return preItem;
		}
		
		/**
		 *  
		 * @param item 
		 * @return 
		 * 
		 */		
		public addItem(item:object):object
		{
			if (!this.checkMainKey(item)) return null;
			const key:string = this.getItemKey(this._mainKeys, item);
			const preItem:object=this.getItemByMainKey(key);
			if(!preItem) {
				this._itemCls!=null && (item=new this._itemCls(item));
				this._dic[key]=item;
				this._values.indexOf(item)==-1 && this._values.push(item);
				this._keys.indexOf(key)==-1 && this._keys.push(key);
				zt.ctrl.GameCtrl.intance.event(DataHash.D_ADD+this.name, item);
				return item;
			}
			return preItem;
		}
	}
}