namespace zt.scene {
	
	export class SceneData
	{
		private _name:string;
		private _gui:Array<any>;
		private _backgroundID:string;

		constructor(obj:object) {
			this._name = obj['name'];
			this._gui = obj['gui'];
			this._backgroundID = obj['backgroundID'];
		}

		get background():string {
			return this._backgroundID;
		}

		set background(value:string) {
			this._backgroundID = value;
		}

		get gui():Array<any> {
			return this._gui;
		}

		set gui(value:Array<any>) {
			this._gui = value;
		}

		get name():string {
			return this._name;
		}

		set name(value:string) {
			this._name = value;
		}
	}
}