namespace zt
{
    import GComponent = fairygui.GComponent ;
    import Sprite = laya.display.Sprite ;
    import Event = Laya.Event;
    import AppBase = zt.app.AppBase;
    import Handler = Laya.Handler ;
    /**
     * @author wangcongyan
     * @usage 
     * @since 2017.12.22
     */
    export class Adaptation
    {   
        private static _ins:Adaptation;
        public static MAX_SCREEN_W:number = 1920 ;
        public static MAX_SCREEN_H:number = 1080 ;
        public static MIN_SCEEN_W:number = 1280 ;
        public static MIN_SCEEN_H:number = 768 ;

        private static _centerTarget:object ;
        private static _fullTarget:object ;
        private static _borderTarget:object ;
        private static _posTarget:object ;
        private static _allTarget:object ;
        private static _funcTarget:object;
        private static _handleParam:object;
        constructor()
        {
        
        }


        /**
         * 
         */
        public static init():void
        {
            Adaptation._centerTarget = {};
            Adaptation._fullTarget = {};
            Adaptation._borderTarget = {};
            Adaptation._posTarget = {};
            Adaptation._funcTarget = {};
            Adaptation._handleParam = {};
            Adaptation._allTarget = {1:Adaptation._centerTarget,4:Adaptation._fullTarget,8:Adaptation._borderTarget,16:Adaptation._posTarget};
            Adaptation.addEvent();
        }

        /**
         * 
         */
        public static addEvent():void
        {
            Laya.stage.on(Event.RESIZE,this,this.resizeView);
            // ctrl.GameCtrl.intance.on()
        }

        /**
         * 
         */
        public static resizeView():void
        {
            const centerTarget:object = Adaptation._centerTarget ;
            const fullTarget :object = Adaptation._fullTarget ;
            const borderTarget :object= Adaptation._borderTarget;
            const funcTarget:object = Adaptation._funcTarget ;
     
            let ui:GComponent|Sprite;
            for(let key in centerTarget)
            {
                ui = centerTarget[key] ;
                Adaptation.centerUIAdaptaion(ui as AppBase);
            }
            for(let key in  fullTarget)
            {
                ui = fullTarget[key] ;
                Adaptation.fullUIAdaptaion(ui as AppBase);
            }
            for(let key in borderTarget)
            {
                ui = fullTarget[key] ;
                Adaptation.fullUIAdaptaion(ui as AppBase);
            }
            for(let key in funcTarget)
            {
                const apply = funcTarget[key];
                apply instanceof Handler ? apply.run():apply();
            }
        }

        /**
         * 
         * @param $ui
         */
        private static fullUIAdaptaion($ui:GComponent|AppBase):void
        {
            const minW = Adaptation.MIN_SCEEN_W ;
            const minH = Adaptation.MIN_SCEEN_H ;
            const maxH = Adaptation.MAX_SCREEN_H;
            const maxW = Adaptation.MAX_SCREEN_W ;
            $ui.scaleX = $ui.scaleY  = 1 ;
            const uiW:number = $ui.width ;
            const uiH:number = $ui.height ;
            const stageW:number = Laya.stage.width ;
            const stageH:number = Laya.stage.height ;        
            if(uiW<stageW)
            {
                $ui.width = stageW ;
            }
            if(uiH >stageH)
            {
                $ui.height = stageH ;
            }
            if(uiW > stageW)
            {
                $ui.width = minW ;
            }
            if(uiH >stageH)
            {
                $ui.height = minH ;
            }
            $ui.x = 0 ;
            $ui.y = 0 ;
            const funcParam:object = Adaptation._handleParam;
            const key = $ui instanceof GComponent ? $ui.resourceURL:$ui.moduleName;
            (funcParam[key] as Handler).run();
        }

        /**
         * 
         * @param $ui
         */
        private static centerUIAdaptaion($ui:AppBase):void
        {
            const minW = Adaptation.MIN_SCEEN_W ;
            const minH = Adaptation.MIN_SCEEN_H ;
            const maxH = Adaptation.MAX_SCREEN_H;
            const maxW = Adaptation.MAX_SCREEN_W;
            $ui.scaleX = $ui.scaleY = 1;
            const rect:Laya.Rectangle = $ui.getAppBounds();
            const uiW:number = rect.width ;
            const uiH:number = rect.height ;
            const stageW:number = Laya.stage.width ;
            const stageH:number = Laya.stage.height ; 
            $ui.x = stageW - uiW >> 1 ;
            $ui.y = stageH - uiH >> 1 ;
            const scaleX:number = stageW > maxW ?  stageW/maxW : 1 ;
            const scaleY:number = stageH > maxH ?  stageH/maxH : 1 ;
            if(uiW > stageW && uiH > stageH)
            {
                $ui.width = stageW ;
                $ui.height = stageH;
            }
        }


        /**
         * 
         */
        public static removeEvent():void
        {
            Laya.stage.on(Event.RESIZE,this,this.resizeView);
        }

        /**
         * 
         * @param $target 
         * @param $adaptType p
         */
        public static register($ui:GComponent|AppBase,$adaptType:number = AdaptationType.ADAPT_CENTER,handler?:Handler):void
        {   
            const target = Adaptation._allTarget[$adaptType];
            const key = $ui instanceof GComponent ? $ui.resourceURL:$ui.moduleName;
            target[key] = $ui;
            if(handler)
            Adaptation._handleParam[key] = handler;
        }


        /**
         * 
         * @param key
         * @param target 
         */
        public static registerFunc($key:string,target:Function|Handler):void
        {   
            Adaptation._funcTarget[$key] = target ;
        }

        public static unregisterFunc($key:string):void
        {   
            const funcTarget = Adaptation._funcTarget ;
            const apply = funcTarget[$key];
            apply instanceof Handler ? apply.recover():console.log('adaptation');
            delete Adaptation._funcTarget[$key];
        }

        /**
         * 
         */
        public static unregister($target:GComponent|AppBase,$adaptType:number = AdaptationType.ADAPT_CENTER):void
        {
            const target  = Adaptation._allTarget[$adaptType];
            if(!!target == false)
            {
               Tips.showRaisingTips('');
               return ;
            }
            const key = $target instanceof GComponent ? $target.resourceURL:$target.moduleName;
            delete target[key];
        }
    }


    /**
     * 
     */
    export class AdaptationType 
    {
        /**
         * 
         */
        static ADAPT_CENTER:number = 1;

        /**
         * 
         */
        static ADAPT_FULL_SCREEN:number = 4;

        /**
         * 
         */
        static ADAPT_CLOSE_BORDER:number =  8 ;

        /**
         * 
         */
        static ADAPT_OPEN_POS:number =  16 ;

    }
    
}