namespace zt {
    import Handler = Laya.Handler;
    import call = zt.net.call;
    export class Calls extends Laya.EventDispatcher {
        private _list: { cmd: string, params, data?}[];
        private _counter = 0;
        constructor() {
            super();
            this._list = [];
        }

        add(cmd: string, params) {
            this._list.push({ cmd: cmd, params: params });
        }

        addCmds(list: { cmd: string, params, data?}[]) {
            this._list = this._list.concat(list);
        }

        start() {
            if (!this._list || this._list.length === 0) {
                this.event(Laya.Event.COMPLETE);
                return;
            }
            this._list.forEach(i => {
                call(i.cmd, i.params, Handler.create(this, (data) => {
                    this.check();
                    i.data = data;
                }), Handler.create(this, () => {
                    this.check();
                }));
            })
        }

        private check() {
            this._counter++;
            if (this._counter == this._list.length) {
                this.event(Laya.Event.COMPLETE);
            }
        }

        get list() { return this._list }
    }
}