// tslint:disable:no-console
namespace zt {
    import LocalStorage = laya.net.LocalStorage;

    export const __app = (data: string): void => {
        LocalStorage.setItem('default_app', data);
        zt.reload();
    }
    export const __getapp = () => LocalStorage.getItem('default_app');
    export const __scene = (name: string): void => {
        LocalStorage.setItem('default_scene', name);
        zt.reload();
    }
    export const __getscene = () => LocalStorage.getItem('default_scene');

    /**
     * 
     * @param name 
     */
    export const __disable = (names: string | string[]): void => {
        if (typeof names === 'string')
            names = [names];
        for (const name of names)
            LocalStorage.setItem(name, '0');
        zt.reload();
    }

    export const __enable = (key: string): void => {
        LocalStorage.setItem(key, '1');
        zt.reload();
    }
    export const __enablePlayGround = (key: string): void => {
        __enable('playground');
    }

    export const __enableDebugPanel = (): void => {
        LocalStorage.setItem('debugLibs', 'laya.ani,laya.ui,laya.debugtool');
        __enable('debugPanel');
    }

    export const __disableDebugPanel = (): void => {
        LocalStorage.setItem('debugLibs', '');
        __disable('debugPanel');
    }

    /**
     * 
     * @param key 
     */
    export const __getDebugEnabled = (key: string): boolean =>
        LocalStorage.getItem(key) === '1';

        export const __getEnabled = (key: string): boolean =>
        LocalStorage.getItem(key) === '1' || LocalStorage.getItem(key) === null;

    export const __clear = () => {
        LocalStorage.clear();
        zt.reload();
    }

    export const __clearAndDisable = (names?: string | string[]) => {
        names = names || ['amf', 'gm'];
        LocalStorage.clear();
        __disable(names);
        zt.reload();
    }

    export const __cb = (cmd: string, params: any) => {
        __cbEntity(cmd, params);
    }

    const __cbEntity = (cmd: string, params: any) => {
        const eventname: string = zt.net.EventUtils.getSeqEventName(cmd);
        zt.net.GameSocket.instance.event(eventname, params);
    }
    export const __notify = (cmd: string, params: any) => {
        __notifyEntity(cmd, params);
        console.log(`[DEBUG Notify] ${data}`);
    }

    const __notifyEntity = (cmd: string, params: any) => {
        zt.net.GameSocket.instance.event(cmd, params);
    }
}
