namespace zt {
	/**
	 *  
	 * 1scenemaskscene
	 * debug
	 * 2scenescene
	 * 3scene
	 * 4maskapploading mask
	 */
	export class Scene {

		private static _current: scene.SceneItem;
		private static _enterScene: scene.SceneItem;
		private static _prevParams: SceneParams;
		private static _switchSceneing:boolean = false ;// 
		public static CHANGESCENE:string = 'ChangeScene';

		/**
		 * 
		 * 1
		 * 2
		 * 3
		 * 4 
		 */
		static enter(name: string, params: any = null): void 
		{
			if(Scene._switchSceneing)return;
			if(Scene._current)
				Scene._prevParams = Scene._current.params ;
			if(Scene._current) 
			{
				if (Scene._current.name == name) //update
				{ 
					Scene.update(params);
				} else //change
				{ 
					Scene._current.scene.dispose();
					Scene._switchSceneing = true ;
					Scene.change(name, params);
				}
			} else 
			{   
				Scene.load(name, params);
			}
		}

		/**
		 * 
		 * @param pos 
		 * @param layerName 
		 */
		static move(pos: ScenePos, layerName: string, params) {
			if (pos.x > 0 || pos.y > 0)
				throw new Error('x or y must be minus');
			Scene._current.scene.move(pos, layerName, params || {});
		}

		private static change(name, params: any): void {
			Scene.load(name, params);
		}

		/**
		 * 
		 * @param name 
		 */
		private static update(params: any): void {
			Scene._current.update(params);
			Scene._current.scene.once('complete', this, () => {
				Scene.onBuildComplete();
			});
		}

		/**
		 * scenescene
		 * @param name
		 */
		private static load(name: string, params: any): void 
		{
			const onAppDefinitionLoad = (name: string, scene: scene.SceneBase): void => 
			{
				scene.params = params;
				Scene._enterScene = new zt.scene.SceneItem(name, scene, params);
				Scene.onload();
			}

			if (utils.ModuleUtils.hasDefinition(name)) {
				const scene = utils.ModuleUtils.getInstance(name) as scene.SceneBase;
				scene.params = params;
				const sceneItem: scene.SceneItem = new zt.scene.SceneItem(name, scene, params);
				Scene._enterScene = sceneItem;
				Scene.onload();
			} else
				zt.Loader.loadModule(name, Laya.Handler.create(null, onAppDefinitionLoad, [name]));
		}

		public static switchToPreScene():void
		{
			Scene.enter(Scene.prevParams.sceneName,Scene.prevParams);
		}

		/**
		 * 
		 */
		private static onload(): void 
		{
			Scene._enterScene.once(scene.SceneEvent.PRELOAD_COMPLETE, null, Scene.onPreloadComplete);
			Scene._enterScene.preload();
		}

		private static onPreloadComplete(): void {
			Scene._current && Scene._current.dispose();
			Scene._current = Scene._enterScene;
			Scene._enterScene = null;
			Scene._current.scene.once('complete', this, () => {
				Scene.onBuildComplete();
			});
			Scene._current.scene.build();
			Scene._switchSceneing = false ;
		}

		private static onBuildComplete() 
		{
            ctrl.GameCtrl.intance.event(Scene.CHANGESCENE);//
		}

		static get current(): scene.SceneItem { return Scene._current };
		static get prevParams(): SceneParams { return Scene._prevParams };
	}
}