namespace zt.app {
	export class AppCaches {
		private _apps: Object;
		private _levelApps: { [level: number]: string[] };//
		private _mask: Sprite;//
		private static DEFAULT_COLOR = '#000000';
		private _color: string;
		private _alpha: number;

		constructor() {
			this._apps = {};
			this._levelApps = {};
			this.resetMask();
		}

		public add(name: string, app: AppBase): AppItem 
		{
			const appItem: AppItem = new AppItem(name, app);
			if (this.getItem(appItem.name) == null) {
				this._apps[appItem.name] = appItem;
			}
			if (this._levelApps[appItem.level] == null) {
				this._levelApps[appItem.level] = [];
			}
			if (this._levelApps[appItem.level].indexOf(name) < 0) {
				this._levelApps[appItem.level].push(name);
			}
			appItem.once(AppEvent.DISPOSE, this, this.onAppDispose, [appItem]);
			appItem.once(AppEvent.PRELOAD_COMPLETE, this, this.onAppPreloadComplete, [appItem]);
			return appItem;
		}

		public closeLevelApps(level: number): void {
			if (this._levelApps[level] == null) {
				return;
			}
			for (const name of this._levelApps[level]) {
				const appItem = this.getItem(name);
				appItem.onStartDispose();
			}
		}

		public closeAllApp(): void {
			for (const name in this._apps) {
				const appItem: AppItem = this._apps[name];
				appItem.onStartDispose();
			}
		}

		/** */
		public get isMultiLevel(): boolean {
			let num = 0;
			for (const lv in this._levelApps) {
				if (this._levelApps[lv] != null && this._levelApps[lv].length > 0) {
					num++;
				}
			}
			return num > 1 ? true : false;
		}

		/** */
		public get currLevelApp(): AppItem {
			let currItem: AppItem;
			let level = 0;
			for (const lv in this._levelApps) {
				const tempLv = parseInt(lv);
				if (this._levelApps[lv] != null && this._levelApps[lv].length > 0 && tempLv >= level) {
					level = tempLv;
					currItem = this.getItem(this._levelApps[lv][0]);
				}
			}
			return currItem;
		}

		private onAppDispose(appItem: AppItem) {
			delete this._apps[appItem.name];
			if (this._levelApps[appItem.level] != null) {
				const index = this._levelApps[appItem.level].indexOf(appItem.name);
				if (index >= 0) {
					this._levelApps[appItem.level].splice(index, 1);
				}
			}
			zt.ctrl.GameCtrl.intance.event(AppEvent.APP_DISPOSE,appItem.name);
			this.updateLevelMask();
		}

		private onAppPreloadComplete(appItem: AppItem) {
			this.updateLevelMask();
		}

		public getItem(name: string): AppItem {
			return this._apps[name];
		}

		/**
		 * app
		 */
		appCloseCheck(applevel: number) {
			if (applevel == app.AppLevel.LEVEL_0) {
				this.closeAllApp();
			}
			else {
				this.closeLevelApps(applevel);
			}
		}

		/** */
		private updateLevelMask(): void {
			if (this.isMultiLevel) {//levelappappappapp
				this.showMask();
			}
			else {
				this.hideMask();
			}
		}

		private showMask(): void {
			if (this._mask == null) {
				this._mask = new Sprite();
				this._mask.alpha = this._alpha;
				this._mask.mouseThrough = true;
				this._mask.on(Laya.Event.MOUSE_DOWN, this, (e: Laya.Event) => {
					e.stopPropagation();
				})
				Adaptation.registerFunc('app_mask',() => {
					this._mask.graphics.clear();
					this._mask.graphics.drawRect(0, 0, Laya.stage.width, Laya.stage.height, this._color);
				});
				this._mask.graphics.drawRect(0, 0, Laya.stage.width, Laya.stage.height, this._color);
			}
			this._mask.visible = true;
			Layers.getLayer('app').addChild(this._mask);
			const currItem = this.currLevelApp;
			const index = Layers.getLayer('app').getChildIndex(currItem.app);
			if (index >= 0) {
				currItem.topup();
			}
		}

		private hideMask(): void {
			if (this._mask != null) {
				this._mask.visible = false;
			}
		}

		updateMask(color, alpha) {
			this._color = color;
			this._alpha = alpha;
			if (this._mask) {
				this._mask.alpha = alpha;
				this._mask.graphics.clear();
				this._mask.graphics.drawRect(0, 0, Laya.stage.width, Laya.stage.height, this._color);
			}
		}

		resetMask() {
			this._color = AppCaches.DEFAULT_COLOR;
			this._alpha = DEFAULT_MASK_ALPHA;
			if (this._mask) {
				this._mask.alpha = this._alpha;
				this._mask.graphics.clear();
				this._mask.graphics.drawRect(0, 0, Laya.stage.width, Laya.stage.height, this._color);
			}
		}
	}
}