namespace zt.app.core{
    export class STView
    {
        private static _instance:STView ;
        private _viewMap:object ;
        constructor()
        {
            this._viewMap = {};
        }

        public static getInstance():STView
        {
            if(!STView._instance)
            {
                STView._instance = new STView();
            }
            return STView._instance;
        }

        /**
         * 
         * @param uiName 
         */
        public retrieveUIView(uiName:string):IView
        {
            return this._viewMap[uiName];
        }

        /**
         * 
         * @param  
         */
        public registerUIView($uiView:IView):void
        {
            const uiviewMap:object = this._viewMap;
            if(!!uiviewMap[$uiView.uIName])return ;
            uiviewMap[$uiView.uIName] = $uiView ;
            $uiView.init();
        }

        public removeUIView(uiName:string):IView
        {
            const view = this._viewMap[uiName];
            if(view)
            {
                view.dispose();
                delete this._viewMap[uiName];
            }
            return view ;
        }
    }
}