namespace zt {
	/**
	 * app
	 * 1
	 * 2
	 * app
	 * 1appmaskapp
	 * debug
	 * 2appapp
	 * 3app
	 * 4maskapploading mask
	 * @author Administrator
	 */
	import AppItem = zt.app.AppItem;
	import AppBase = zt.app.AppBase;
	import AppCaches = zt.app.AppCaches;

	export class App {
		/**
		 * appapp
		 */
		static load(name: string, params?: any, cb?: Laya.Handler, noLoading = false) {
			const current = App.appCaches.getItem(name);
			if (current) {//app
				App.topup(current);
				return;
			}
			App._currAppName = name;
			const onAppDefinitionLoad = (name: string, app: AppBase): void => {
				App.appCaches.appCloseCheck(app.level);
				app.moduleName = name ;
				const appItem = App.appCaches.add(name, app);
				app.params = params;
				appItem.start(noLoading);
				cb && cb.runWith(appItem);
			}

			if (utils.ModuleUtils.hasDefinition(name)) { //appapp
				const app: AppBase = utils.ModuleUtils.getInstance(name) as AppBase;
				onAppDefinitionLoad(name, app);
			} else
				Loader.loadModule(name, Laya.Handler.create(null, onAppDefinitionLoad, [name]));
		}

		static updateMask(color, alpha) {
			App.appCaches.updateMask(color, alpha);
		}
		static resetMaskColor() {
			App.appCaches.resetMask();
		}

		private static _currAppName;
		private static topup(app: AppItem) { app.topup(); }

		private static _appCaches: AppCaches;
		public static get appCaches(): AppCaches {
			return this._appCaches || (this._appCaches = new AppCaches())
		}
	}
}