namespace zt.data {
	
	/**
	 *  
	 */	
	export class DataItem extends BaseDataItem {

		constructor(d:object) {
			super(d);
		}

		hasUpdate:number = 1;
		
		public update(d:object):void {
			if(!d)return;
			for(const key in d) {
				if ('_'+key in this) this['_'+key] = d[key];
				else this.setPropRead(key,d);
			}
		}

		public updataByMode(d:object,p:Array<string>=[]): void {
			if(!d)return;
			for(const key in d) {
				if (p.length == 0 || p.indexOf(key) == -1) continue;
				switch(d['Mode']) {
					case 'Sub':
						if ('_'+key in this) this['_'+key] -= d[key];	
						break;
					case 'Add':
						if ('_'+key in this) this['_'+key] += d[key];
						break;
				}
			}
		}
	}
}