namespace zt.universe.view {
    import EventDispatcher = Laya.EventDispatcher;
    import Event = Laya.Event;
    export class ViewPort extends EventDispatcher {
        private static _viewPort: ViewPort;
        private _xPercent: number;
        private _yPercent: number;

        public static onChange(caller: any, listener: Function) {
            ViewPort._viewPort.on(Event.CHANGE, caller, listener);
        }

        /**
         * @param xPercent viewport
         * @param yPercent viewport
         * @param trigger 
         */
        public static change(xPercent: number, yPercent: number, trigger:string) {
            ViewPort._viewPort._xPercent = xPercent;
            ViewPort._viewPort._yPercent = yPercent;
            ViewPort._viewPort.event(Event.CHANGE, {viewPort:ViewPort._viewPort, trigger:trigger});
        }

        public get xPercent(): number { return this._xPercent; }
        public get yPercent(): number { return this._yPercent; }

        public static init(): void {
            ViewPort._viewPort = new ViewPort();
        }
    }
}