namespace zt {
    import GComponent = fairygui.GComponent;
    export class UIBase implements AppUIBase{
        protected _ui: GComponent;
        constructor(ui: GComponent) 
        {
            this._ui = ui;
        }

        set visible(val: boolean) {
            this._ui.displayObject.visible = val
        }


        get ui(): GComponent { return this._ui }

        start() 
        {
            throw Error('must be overrided') 
        }

        update() {
            //
        }

        dispose()
        {
            throw Error('must be overrided')
        }
    }

    export interface AppUIBase {
        start();
        dispose()
    }
}